import React, { Component } from "react";
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";
import Navbar from "../front/navbar";
import Paper from "@material-ui/core/Paper";
import { NavLink } from "react-router-dom";
import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";
import Footer from "../front/footer";
import { GoogleApiWrapper } from "google-maps-react";
import MapContainer from "./googlemap";

class Contact extends Component {
  render() {
    return (
      <div className="NavBar">
        <div>
          <Navbar />
          <Paper
            style={{
              backgroundImage: "url(" + require("../front/b.jpg") + ")",
              backgroundSize: "cover",
              textAlign: "center",
              height: "42vh",
              verticalAlign: "middle"
            }}
          >
            <div
              style={{
                height: "100%",
                verticalAlign: "middle",
                backgroundColor: "rgba(111,50,158,0.7)"
              }}
            >
              <Typography
                style={{
                  padding: "80px",
                  color: "white",
                  fontSize: "40px",
                  fontWeight: "bold"
                }}
              >
                Contact Us
              </Typography>
            </div>
          </Paper>

          <Grid container>
            <Grid
              item
              lg={5}
              md={12}
              sm={12}
              xl={5}
              xs={12}
              style={{ margin: "30px", textAlign: "center" }}
            >
              <div
                style={{
                  position: "sticky",
                  height: "600px",
                  width: "600px",
                  textAlign: "center"
                }}
              >
                <MapContainer />
              </div>
            </Grid>
            <Grid
              item
              lg={5}
              md={12}
              sm={12}
              xl={5}
              xs={12}
              style={{ margin: "30px", textAlign: "center" }}
            >
              <Typography variant="h5" style={{ fontWeight: "bold" }}>
                Have you checked out our FAQs?
              </Typography>
              <Typography
                style={{ margin: "20px", fontSize: "18px", fontWeight: "bold" }}
              >
                If your question wasn't covered, use the form here to ask it and
                we'll send our response at earliest opportunity!
              </Typography>

              <form noValidate autoComplete="off">
                <TextField
                  style={{ width: "100%", marginTop: "30px" }}
                  label="Full Name"
                  placeholder="Full Name"
                  margin="normal"
                  variant="outlined"
                />
                <TextField
                  style={{ width: "100%", marginTop: "30px" }}
                  label="Email Address"
                  placeholder="Email Address"
                  margin="normal"
                  variant="outlined"
                />
                <TextField
                  style={{ width: "100%", marginTop: "30px" }}
                  label="Phone"
                  placeholder="Phone"
                  margin="normal"
                  variant="outlined"
                />
                <TextField
                  style={{ width: "100%", marginTop: "30px" }}
                  label="Describe your Question"
                  placeholder="Describe Your Question Here"
                  margin="normal"
                  variant="outlined"
                  multiline
                />
                <Button
                  className="btn btn-primary jr-btn-rounded jr-btn-sm"
                  variant="contained"
                  color="primary"
                  style={{ width: "100%", padding: "20px", margin: "30px 0" }}
                >
                  SEND
                </Button>
              </form>
            </Grid>
          </Grid>

          <Paper
            style={{
              backgroundImage: "url(" + require("../front/b.jpg") + ")",
              backgroundSize: "cover",
              textAlign: "center",
              height: "46vh",
              verticalAlign: "middle",
              marginTop: "150px",
              marginBottom: "-100px"
            }}
          >
            <div
              style={{
                height: "100%",
                verticalAlign: "middle",
                backgroundColor: "rgba(111,50,158,0.7)"
              }}
            >
              <Typography
                style={{
                  padding: "50px",
                  color: "white",
                  fontSize: "40px",
                  fontWeight: "bold"
                }}
              >
                Join our community today !{" "}
              </Typography>
              <NavLink
                to="/signup"
                style={{
                  fontSize: "1vw",
                  padding: "auto",
                  margin: "auto",
                  textDecoration: "none",
                  color: "black"
                }}
              >
                <Button
                  className="btn btn-primary jr-btn-rounded jr-btn-sm"
                  variant="contained"
                  color="primary"
                >
                  CREATE A FREE ACCOUNT
                </Button>
              </NavLink>
            </div>
          </Paper>
        </div>
        <Footer />
      </div>
    );
  }
}

export default Contact;
