import React, { Component } from "react";
import AppBar from "@material-ui/core/AppBar";
import ToolBar from "@material-ui/core/Toolbar";
import Grid from "@material-ui/core/Grid";
import Cards from "./cards.js";
import firebase from "firebase";
class Blocks extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: [],
      urlsmain: [],
      mainscene: null
    };
    this.fetchingfirebase = this.fetchingfirebase.bind(this);
  }
  async componentDidMount() {
    await firebase
      .database()
      .ref("/blog")
      .once("value", snapshot => {
        var a = snapshot.val();
        var main = [];
        if (a.length !== 0) {
          const keys = Object.keys(a);
          for (let i = 0; i < keys.length; i++) {
            main.push([a[keys[i]]]);
          }
          this.setState({
            data: main,
            urlsmain: []
          });
        }
      });

    this.fetchingfirebase(0);
  }

  async fetchingfirebase(i) {
    if (i < this.state.data.length) {
      var storageref = firebase.storage().ref("/blog");

      await storageref
        .child(this.state.data[i][0].titleblog)
        .child(this.state.data[i][0].titleblog)
        .getDownloadURL()
        .then(url => {
          this.setState(prevState => ({
            urlsmain: [...prevState.urlsmain, url]
          }));
          this.fetchingfirebase(i + 1);
        });
    } else {
      return;
    }
  }

  render() {
    var i = -1;
    const main =
      this.state.urlsmain.length == this.state.data.length ? (
        this.state.data.map(data => {
          i = i + 1;
          return (
            <Cards
              images={this.state.urlsmain[i]}
              detailsblog={data[0].detailsblog.substring(0, 300)}
              byblog={data[0].byblog}
              dateblog={data[0].dateblog}
              title={data[0].titleblog}
            />
          );
        })
      ) : (
        <div> </div>
      );

    return (
      <div>
        <Grid container>{main}</Grid>
      </div>
    );
  }
}

export default Blocks;
