import React, { Component } from "react";
import firebase from "firebase";
import Button from "@material-ui/core/Button";

class NewsTest extends Component {
  constructor() {
    super();
    this.handlesubmit = this.handlesubmit.bind(this);
    this.handlesubmitteam = this.handlesubmitteam.bind(this);
    this.handlesubmitnews = this.handlesubmitnews.bind(this);

    this.handlesubmitblog = this.handlesubmitblog.bind(this);
  }
  handlesubmit(e) {
    e.preventDefault();
    const file = e.target[0].files[0];
    const storage = firebase.storage();
    const storageref = storage.ref("/carousal").child(this.refs.name.value);
    storageref
      .child(this.refs.name.value)
      .put(file, { contentType: file.type });

    var item = {
      name: this.refs.name.value,
      cat: this.refs.cat.value,
      industry: this.refs.industry.value,
      investor: this.refs.investor.value,
      days: this.refs.days.value,
      effective: this.refs.effective.value,
      tenor: this.refs.tenor.value
    };
    const itemRefs = firebase.database().ref("/carousal");
    itemRefs.push(item);
  }
  handlesubmitteam(e) {
    e.preventDefault();
    const file = e.target[0].files[0];
    const storage = firebase.storage();
    const storageref = storage.ref("/team").child(this.refs.nameteam.value);
    storageref
      .child(this.refs.nameteam.value)
      .put(file, { contentType: file.type });

    var item = {
      nameteam: this.refs.nameteam.value,
      category: this.refs.category.value,
      explanation: this.refs.explanation.value
    };
    const itemRefs = firebase.database().ref("/team");
    itemRefs.push(item);
  }
  handlesubmitnews(e) {
    e.preventDefault();
    const storage = firebase.storage;

    var item = {
      title: this.refs.title.value,
      details: this.refs.details.value
    };
    const itemRefs = firebase.database().ref("/news");
    itemRefs.push(item);
  }
  handlesubmitblog(e) {
    e.preventDefault();
    const file = e.target[0].files[0];

    const storage = firebase.storage();
    const storageref = storage.ref("/blog").child(this.refs.titleblog.value);
    storageref
      .child(this.refs.titleblog.value)
      .put(file, { contentType: file.type });
    console.log(e);
    var item = {
      titleblog: this.refs.titleblog.value,
      dateblog: this.refs.dateblog.value,
      byblog: this.refs.byblog.value,
      detailsblog: this.refs.detailsblog.value
    };
    const itemRefs = firebase.database().ref("/blog");
    itemRefs.push(item);
  }
  render() {
    return (
      <div className="NavBar">
        <React.Fragment>
          <h1>This is just a trial page</h1>
          <h1>CAROUSAL</h1>
          <form onSubmit={this.handlesubmit}>
            <input type="file" />
            <input type="text" placeholder="name" ref="name" />
            <input type="text" placeholder="cat" ref="cat" />
            <input type="text" placeholder="industry" ref="industry" />
            <input type="text" placeholder="Investor" ref="investor" />
            <input type="text" placeholder="Days" ref="days" />
            <input type="text" placeholder="Effective" ref="effective" />
            <input type="text" placeholder="tenor" ref="tenor" />
            <button>ADD</button>
          </form>
          ********************************************************
          <h1>TEAM</h1>
          <form onSubmit={this.handlesubmitteam}>
            <input type="file" />
            <input type="text" placeholder="name" ref="nameteam" />
            <input type="text" placeholder="cat" ref="category" />
            <input type="text" placeholder="industry" ref="explanation" />
            <button>ADD</button>
          </form>
          *********************************************************
          <h1>NEWS</h1>
          <form onSubmit={this.handlesubmitnews}>
            <input type="text" placeholder="title" ref="title" />
            <textarea type="text" placeholder="details" ref="details" />
            <button>ADD</button>
          </form>
          *********************************************************
          <h1>BLOG</h1>
          <form onSubmit={this.handlesubmitblog}>
            <input type="file" />
            <input type="text" placeholder="title" ref="titleblog" />
            <input type="text" placeholder="date" ref="dateblog" />
            <input type="text" placeholder="by" ref="byblog" />
            <input type="text" placeholder="details" ref="detailsblog" />
            <button>ADD</button>
          </form>
        </React.Fragment>
      </div>
    );
  }
}

export default NewsTest;
