import Button from "@material-ui/core/Button";
import firebase from "firebase";
import React from "react";
import { Card, CardBody, CardText } from "reactstrap";
var user = firebase.auth().currentUser;
class Verify extends React.Component {
  state = {
    e_sent: false
  };
  componentDidMount() {
    console.log(user, "data of the user");
    user = firebase.auth().currentUser;
    this.setState({ userData: user });
    if (user && user.emailVerified === true) {
      this.setState({ Verify: true });
    }
  }
  onVerifyEmail = () => {
    let tempThis = this;
    user
      .sendEmailVerification()
      .then(function() {
        tempThis.setState({ e_sent: true });
      })
      .catch(function(error) {
        console.log("An error happened.", error);
      });
  };
  componentDidUpdate() {
    user = firebase.auth().currentUser;
  }
  render() {
    if (!this.state.userData) {
      return (
        <div className="d-flex justify-content-center align-items-center">
          <Card className="shadow border-0">
            <CardBody>
              <CardText>User not found(Please Refresh)</CardText>
            </CardBody>
            <div className="card-mt-footer">
              <Button
                href={"/"}
                className="btn btn-primary jr-btn-rounded jr-btn-sm"
              >
                Refresh
              </Button>
            </div>
          </Card>
        </div>
      );
    } else {
      if (!user.emailVerified) {
        return (
          <div className="d-flex justify-content-center align-items-center">
            <Card className="shadow border-0">
              <CardBody>
                <CardText>
                  {this.state.e_sent === true
                    ? "A mail has been sent to Your Email ID "
                    : "Please click the below button to resend the verification email."}
                </CardText>
              </CardBody>
              <div className="card-mt-footer">
                <Button
                  href="javascript:void(0)"
                  onClick={this.onVerifyEmail}
                  className="btn btn-primary jr-btn-rounded jr-btn-sm"
                >
                  {this.state.e_sent === false ? "Send Email" : "Resend Email"}
                </Button>
              </div>
            </Card>
          </div>
        );
      } else {
        return (
          <div className="d-flex justify-content-center align-items-center">
            <Card className="shadow border-0">
              <CardBody>
                <CardText>Your Email has been verified</CardText>
              </CardBody>
            </Card>
          </div>
        );
      }
    }
  }
}

export default Verify;
