class GetStepContent extends React.Component {
  constructor(props) {
    super();
  }
  render() {
    switch (this.props.activeStep) {
      // case 0:
      //     return <Application handleChange={this.props.handleChange} data={this.props.data} nextPage={this.props.nextPage}/>;
      case 0:
        return (
          <Overview
            handleChange={this.props.handleChange}
            data={this.props.data}
            id={this.props.id}
            nextPage={this.props.nextPage}
          />
        );
      case 1:
        return (
          <Photo_Video
            handleChange={this.props.handleChange}
            data={this.props.data}
            id={this.props.id}
            nextPage={this.props.nextPage}
          />
        );
      case 2:
        return (
          <Financials
            handleChange={this.props.handleChange}
            data={this.props.data}
            id={this.props.id}
            close={this.props.close}
            nextPage={this.props.nextPage}
          />
        );
      // case 4:
      //     return <Management handleChange={this.props.handleChange} data={this.props.data} nextPage={this.props.nextPage}/>;
      // case 5:
      //     return <Updates handleChange={this.props.handleChange} data={this.props.data} nextPage={this.props.nextPage}/>;
      // case 6:
      //     return <FAQ handleChange={this.props.handleChange} data={this.props.data} nextPage={this.props.nextPage}/>;
      case 3:
        return "";
      default:
        return "Uknown stepIndex";
    }
  }
}

