import React,{Component} from 'react'
import Grid from '@material-ui/core/Grid';
import Typography from "@material-ui/core/Typography"
import Navbar from '../front/navbar'
import Paper from '@material-ui/core/Paper'
import Button from '@material-ui/core/Button'
import TextField from '@material-ui/core/TextField'
import Footer from '../front/footer'
import {Card,CardHeader,CardActions,CardMedia,CardContent,Avatar,Collapse,IconButton} from '@material-ui/core'

 class Cards extends Component{
    constructor(){
        super();
        this.state={
            expanded:false,
        }
    }
    handleExpandClick = () => {
        this.setState((state) => ({ expanded: !state.expanded }));
      };
    render(){
       

       
        return(
            
  <Card  raised style={{margin:"20px"}} >
        <CardHeader
          title={this.props.title}
        />
        <CardMedia
          image={this.props.images}
          title={this.props.imagetitle}
        />
        <CardContent>
          <Typography component="p">
            {this.props.highlight}
          </Typography>
        </CardContent>
        <CardActions  disableActionSpacing>
          <IconButton
            onClick={this.handleExpandClick}
            aria-expanded={this.state.expanded}
            aria-label="Show more"
          >
           <i className="far fa-caret-square-down"></i>
          </IconButton>
        </CardActions>
        <Collapse in={this.state.expanded} timeout="auto" unmountOnExit>
          <CardContent>
            <Typography paragraph>Explaination :</Typography>
            <Typography paragraph>
             {this.props.para1}
            </Typography>
            <Typography paragraph>
              {this.props.para2}
            </Typography>
            <Typography paragraph>
              {this.props.para3}
            </Typography>
            <Typography>
              {this.props.para4}
            </Typography>
          </CardContent>
        </Collapse>
      </Card>
        )
    }
}

export default Cards;
