import { Hidden } from "@material-ui/core";
import Button from "@material-ui/core/Button";
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";
import React, { Component } from "react";
import LoadingScreen from "react-loading-screen";
import { Fade } from "react-reveal";
import { NavLink } from "react-router-dom";
import logo from "../../logo.svg";
import { default as Carousal } from "./carousal";
import Footer from "./footer.js";
import FrontImage from "./frontimage.js";
import Howit from "./howitworks";
import Navbar from "./navbar.js";
import OurNetWork from "./ournetwork.js";
import { connect } from "react-redux";
import { Redirect } from "react-router-dom";

class home extends Component {
  constructor(props) {
    super();
    this.state = {
      loading: true
    };
  }

  componentDidMount() {
    // the setTimeout just simulates an async action, after which the component will render the content
    setTimeout(() => this.setState({ loading: false }), 2500);
  }

  changeActiveItem = activeItemIndex => this.setState({ activeItemIndex });

  render() {
    const { auth, profile, initURL } = this.props;
    if (auth.isEmpty === false && profile.isLoaded === true) {
      if (this.props.initURL === "/" || this.props.initURL === "/home") {
        return <Redirect to={"/" + profile.profile} />;
      } else {
        return <Redirect to={initURL} />;
      }
    }
    const { loading } = this.state;
    return (
      <div className="NavBar">
        <LoadingScreen
          loading={loading}
          bgColor="#f1f1f1"
          spinnerColor="#9ee5f8"
          textColor="#676767"
          logoSrc={logo}
          text="Getting Your Data"
        >
          <Grid>
            <Navbar />

            <FrontImage />
            <Grid>
              <Typography
                style={{
                  fontSize: "30px",
                  fontWeight: "bold",
                  padding: "50px 0 20px 0",
                  textAlign: "center"
                }}
              >
                Why Crowd Genie ?
              </Typography>
            </Grid>
            <Grid
              container
              lg={12}
              sm={12}
              md={12}
              xl={12}
              style={{ margin: "100px 0 0 0 " }}
            >
              <Grid
                item
                lg={4}
                md={4}
                sm={12}
                xl={12}
                style={{ background: "#662D91", margin: "auto" }}
              >
                <Typography
                  variant="h4"
                  style={{
                    color: "white",
                    fontWeight: "normal",
                    margin: "auto",
                    textAlign: "center",
                    padding: "100px 50px 50px 50px "
                  }}
                >
                  What makes us different
                </Typography>
                <Typography
                  variant="h5"
                  style={{
                    color: "white",
                    margin: "auto",
                    fontWeight: "normal",
                    textAlign: "center",
                    padding: "50px 50px 100px 50px "
                  }}
                >
                  New paradigm in SME lending
                </Typography>
              </Grid>
              <Grid
                item
                lg={4}
                md={4}
                sm={12}
                xl={12}
                style={{
                  border: "25px solid 662D91",
                  background: "white",
                  margin: "auto"
                }}
              >
                <Typography
                  style={{
                    fontSize: "20px",
                    color: "black",
                    fontWeight: "normal",
                    margin: "auto",
                    textAlign: "left",
                    padding: "100px 50px 50px 50px "
                  }}
                >
                  <ul>
                    <li>Flexible: Personal guarantors are not always needed</li>
                    <li>
                      Cheaper: Our top grade borrowers can get rates competitive
                      with those that banks offer
                    </li>
                    <li>
                      Faster: The entire process from listing to getting money
                      can be done in less than 45 days
                    </li>
                  </ul>
                </Typography>
              </Grid>
              <Grid
                container
                lg={12}
                sm={12}
                md={12}
                xl={12}
                style={{ margin: "100px 0 0 0 " }}
              >
                <Grid
                  item
                  lg={4}
                  md={4}
                  sm={12}
                  xl={12}
                  style={{
                    border: "25px solid 662D91",
                    background: "white",
                    margin: "auto"
                  }}
                >
                  <Typography
                    variant="h4"
                    style={{
                      fontWeight: "normal",
                      textAlign: "center",
                      padding: "100px 20px 20px 20px "
                    }}
                  >
                    Borrowers
                  </Typography>
                  <Typography
                    variant="h5"
                    style={{
                      fontWeight: "normal",
                      textAlign: "center",
                      padding: "20px 20px 20px 20px "
                    }}
                  >
                    Small and medium size companies
                  </Typography>
                  <Typography
                    style={{
                      fontSize: "20px",
                      color: "black",
                      fontWeight: "normal",
                      textAlign: "left",
                      margin: "20px",
                      padding: "20px"
                    }}
                  >
                    Small and Medium sized companies who face big obstacles in
                    borrowing cash to run and grow their businesses, such as:
                    <ul>
                      <li>
                        Banks do not want to lend to them as they these
                        companies do not have the perfect credit history
                      </li>
                      <li>
                        Credit approval process takes too long, and is not
                        transparent
                      </li>
                    </ul>
                  </Typography>
                </Grid>

                <Grid
                  item
                  lg={4}
                  md={4}
                  sm={12}
                  xl={12}
                  style={{
                    background: "#662D91",
                    margin: "auto"
                  }}
                >
                  <Typography
                    variant="h4"
                    style={{
                      color: "white",
                      fontWeight: "normal",

                      textAlign: "center",
                      padding: "100px 20px 20px 20px "
                    }}
                  >
                    Investors
                  </Typography>
                  <Typography
                    variant="h5"
                    style={{
                      color: "white",
                      fontWeight: "normal",
                      textAlign: "center",
                      padding: "20px"
                    }}
                  >
                    People looking for better financial returns
                  </Typography>
                  <Typography
                    style={{
                      fontSize: "20px",
                      color: "white",
                      fontWeight: "normal",
                      textAlign: "left",
                      margin: "20px 20px 0px 20px",
                      padding: "20px"
                    }}
                  >
                    The investors seek better transpancey, control, and
                    financial returns.
                  </Typography>

                  <Typography
                    style={{
                      fontSize: "20px",
                      color: "white",
                      fontWeight: "normal",
                      textAlign: "left",
                      margin: "0px 20px 40px 20px",
                      padding: "20px"
                    }}
                  >
                    Currently, even though the banks charge a lot more from the
                    borrowers, investors get only a very low interest rate on
                    their deposits.
                  </Typography>
                </Grid>
              </Grid>
              <Grid
                container
                lg={12}
                sm={12}
                md={12}
                xl={12}
                style={{ margin: "100px 0 0 0 " }}
              >
                <Grid
                  item
                  lg={4}
                  md={4}
                  sm={12}
                  xl={12}
                  style={{ background: "#662D91", margin: "auto" }}
                >
                  <Typography
                    variant="h4"
                    style={{
                      color: "white",
                      fontWeight: "normal",
                      margin: "auto",
                      textAlign: "center",
                      padding: "100px 50px 50px 50px "
                    }}
                  >
                    Borrowers
                  </Typography>
                  <Typography
                    variant="h5"
                    style={{
                      color: "white",
                      margin: "auto",
                      fontWeight: "normal",
                      textAlign: "center",
                      padding: "50px 50px 100px 50px "
                    }}
                  >
                    Faster access to required capital
                  </Typography>
                </Grid>

                <Grid
                  item
                  lg={4}
                  md={4}
                  sm={12}
                  xl={12}
                  style={{
                    border: "25px solid 662D91",
                    background: "white",
                    margin: "auto"
                  }}
                >
                  <Typography
                    style={{
                      fontSize: "20px",
                      color: "black",
                      fontWeight: "normal",
                      margin: "auto",
                      textAlign: "left",
                      padding: "100px 50px 50px 50px "
                    }}
                  >
                    <ul>
                      <li>
                        Initial upfront fees from $2000 for initial company
                        evaluation and listing.
                      </li>
                      <li>8% flat minimum interest rate</li>
                      <li>2% processing fees</li>
                      <li>3-5 day listing approval</li>
                    </ul>
                  </Typography>
                </Grid>
              </Grid>

              <Grid
                container
                lg={12}
                sm={12}
                md={12}
                xl={12}
                style={{ margin: "100px 0 0 0 " }}
              >
                <Grid
                  item
                  lg={4}
                  md={4}
                  sm={12}
                  xl={12}
                  style={{
                    border: "25px solid 662D91",
                    background: "white",
                    margin: "auto"
                  }}
                >
                  <Typography
                    style={{
                      fontSize: "20px",
                      color: "black",
                      fontWeight: "normal",
                      margin: "auto",
                      textAlign: "left",
                      padding: "100px 50px 50px 50px "
                    }}
                  >
                    <ul>
                      <li>15% ++ returns</li>
                      <li>40+ years of risk underwriting experience</li>
                      <li>100% funds held in Escrow</li>
                    </ul>
                  </Typography>
                </Grid>

                <Grid
                  item
                  lg={4}
                  md={4}
                  sm={12}
                  xl={12}
                  style={{
                    background: "#662D91",
                    margin: "auto"
                  }}
                >
                  <Typography
                    variant="h4"
                    style={{
                      color: "white",
                      fontWeight: "normal",
                      margin: "auto",
                      textAlign: "center",
                      padding: "100px 50px 50px 50px "
                    }}
                  >
                    Investors
                  </Typography>
                  <Typography
                    variant="h5"
                    style={{
                      color: "white",
                      margin: "auto",
                      fontWeight: "normal",
                      textAlign: "center",
                      padding: "50px 50px 100px 50px "
                    }}
                  >
                    More bang for your buck
                  </Typography>
                </Grid>
              </Grid>
              <Hidden mdDown>
                <Grid container style={{ margin: "30px auto" }}>
                  <Grid item lg={12} style={{ textAlign: "center" }}>
                    <Typography
                      style={{
                        fontSize: "30px",
                        fontWeight: "normal",
                        textAlign: "center",
                        padding: "100px 50px 50px 50px "
                      }}
                    >
                      How it works
                    </Typography>
                  </Grid>
                  <Grid item lg={12} style={{ textAlign: "center" }}>
                    <Typography variant="h6" style={{ margin: "20px auto" }}>
                      Get your first return in 30 days.
                    </Typography>
                  </Grid>
                  {
                    // <Grid item lg={12}>
                    //         <img style={{margin:'50px 90px'}} src={step1}/>
                    //         <img style={{margin:'50px 50px'}} src={step2}/>
                    //         <img style={{margin:'50px 70px'}} src={step3}/>
                    //         <img style={{margin:'50px 70px'}} src={step4}/>
                    //         <img style={{margin:'50px 70px'}} src={step5}/>
                    // </Grid>
                  }
                  <Grid lg={12} item style={{ padding: " 0 0 0 100px" }}>
                    <Howit />
                  </Grid>
                </Grid>
              </Hidden>
            </Grid>

            <Fade bottom>
              <Grid
                style={{ background: "#E5E5E5", marginTop: "100px" }}
                container
                justify="center"
                lg={12}
                spacing={10}
              >
                <Grid item lg={12}>
                  <Typography
                    style={{
                      fontSize: "30px",
                      fontWeight: "bold",
                      margin: "60px auto",
                      textAlign: "center"
                    }}
                  >
                    Investment Offerings
                  </Typography>
                </Grid>
                <Carousal />
                <Grid container justify="center" lg={12} spacing={10}>
                  <Button
                    className="btn btn-primary jr-btn-rounded jr-btn-sm"
                    color="primary"
                    variant="contained"
                    style={{
                      margin: "20px",
                      justifyItems: "center"
                    }}
                  >
                    <NavLink
                      to="/signup"
                      style={{
                        fontSize: "1vw",
                        textDecoration: "none",
                        color: "white"
                      }}
                    >
                      Sign Up
                    </NavLink>
                  </Button>
                </Grid>
              </Grid>
            </Fade>
          </Grid>
          <Fade bottom>
            <Grid id="partner">
              <Typography
                style={{
                  fontSize: "30px",
                  fontWeight: "bold",
                  padding: "50px 0 20px 0",
                  textAlign: "center"
                }}
              >
                Our Network
              </Typography>
            </Grid>
            <Grid>
              <OurNetWork />
            </Grid>
          </Fade>
          <Footer />
        </LoadingScreen>
      </div>
    );
  }
}

// const background = {
//   backgroundImage: "url(" + require("./b.jpg") + ")",
//   height: "auto",
//   width: "100%",
//   backgroundAttachment: "fixed",
//   backgroundPosition: "center",
//   backgroundRepeat: "no-repeat",
//   backgroundSize: "cover"
// };
const mapStateToProps = ({ url, firebase: { auth, profile } }) => {
  const { initURL } = url;
  return {
    auth,
    profile,
    initURL
  };
};
export default connect(mapStateToProps)(home);
