import React from "react";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import TextField from "@material-ui/core/TextField";
// import IconButton from "@material-ui/core/IconButton";
import Button from "@material-ui/core/Button";
import {
  NotificationContainer,
  NotificationManager
} from "react-notifications";
import IntlMessages from "util/IntlMessages";
import CircularProgress from "@material-ui/core/CircularProgress";
// import {
//   hideMessage,
//   showAuthLoader,
//   userFacebookSignIn,
//   userGithubSignIn,
//   userGoogleSignIn,
//   userSignIn,
//   userTwitterSignIn
// } from "actions/Auth";
import firebase from "firebase";

class SignIn extends React.Component {
  constructor() {
    super();
    this.state = {
      email: "",
      password: "",
      loader: false,
      showMessage: false,
      alertMessage: ""
    };
  }

  componentDidUpdate() {
    if (this.state.showMessage) {
      setTimeout(() => {
        this.setState({ showMessage: false, alertMessage: "" });
      }, 100);
    }
    if (
      this.props.auth.isEmpty === false &&
      this.props.profile.isLoaded === true
    ) {
      console.log(this.props.profile);
      this.props.history.push("/" + this.props.profile.profile);
    }
  }

  userSignIn = ({ email, password }) => {
    this.setState({ loader: true });
    firebase
      .login({ email, password })
      .then(data => {
        console.log(data);
        this.setState({ loader: false });
      })
      .catch(err => {
        this.setState({
          loader: false,
          showMessage: true,
          alertMessage: err.message
        });
      });
  };
  render() {
    const { email, password, showMessage, loader, alertMessage } = this.state;
    return (
      <div className="app-login-container d-flex justify-content-center align-items-center animated slideInUpTiny animation-duration-3">
        <div className="app-login-main-content">
          <div
            className="app-logo-content d-flex align-items-center justify-content-center"
            style={{
              "border-left-width": "thick",
              "border-left-color": "#662d91",
              "border-left-style": "solid",
              background: "white"
            }}
          >
            <Link className="logo-lg" to="/" title="Crowd-genie">
              <img
                src={require("assets/images/logo.jpg")}
                alt="Crowd Genie"
                height="150"
                width="203"
                title="Crowd Genie"
              />
            </Link>
          </div>

          <div className="app-login-content">
            <div className="app-login-header mb-4">
              <h1>
                <IntlMessages id="appModule.email" />
              </h1>
            </div>

            <div className="app-login-form">
              <form>
                <fieldset>
                  <TextField
                    label={<IntlMessages id="appModule.email" />}
                    fullWidth
                    onChange={event =>
                      this.setState({ email: event.target.value })
                    }
                    defaultValue={email}
                    margin="normal"
                    className="mt-1 my-sm-3"
                  />
                  <TextField
                    type="password"
                    label={<IntlMessages id="appModule.password" />}
                    fullWidth
                    onChange={event =>
                      this.setState({ password: event.target.value })
                    }
                    defaultValue={password}
                    margin="normal"
                    className="mt-1 my-sm-3"
                  />

                  <div className="mb-3 d-flex align-items-center justify-content-between">
                    <Button
                      onClick={() => {
                        this.userSignIn({
                          email: email,
                          password: password
                        });
                      }}
                      variant="contained"
                      className="btn btn-primary jr-btn-rounded jr-btn-sm"
                      color={"primary"}
                    >
                      <IntlMessages id="appModule.signIn" />
                    </Button>

                    <Link to="/signup">
                      <IntlMessages id="signIn.signUp" />
                    </Link>
                  </div>
                </fieldset>
              </form>
            </div>
          </div>
        </div>
        {loader && (
          <div className="loader-view">
            <CircularProgress />
          </div>
        )}
        {showMessage && NotificationManager.error(alertMessage)}
        <NotificationContainer />
      </div>
    );
  }
}

// const mapStateToProps = ({ auth }) => {
//   const { loader, alertMessage, showMessage, authUser } = auth;
//   return { loader, alertMessage, showMessage, authUser };
// };

// export default connect(
//   mapStateToProps,
//   {
//     userSignIn,
//     hideMessage,
//     showAuthLoader,
//     userFacebookSignIn,
//     userGoogleSignIn,
//     userGithubSignIn,
//     userTwitterSignIn
//   }
// )(SignIn);
export default connect(
  // Map state to props
  ({ firebase: { auth, profile } }) => ({
    auth,
    profile
  })
)(SignIn);
