  render() {
    if (this.props.result) {
      let i = 0,
        list = [],
        list_id = [];
      this.props.result.map(doc => {
        list[i] = [
          doc.username,
          moment(doc.createdAt.toDate())
            .format("YYYY/MM/DD")
            .toString(),
          doc.type === undefined ? " " : doc.type,
          doc.amount !== undefined && doc.amount > 0
            ? addZeroes(doc.amount.toString())
            : "",
          doc.balance === undefined
            ? " "
            : addZeroes(doc.balance.toFixed(2).toString()),
          i
        ];
        list_id[i] = doc.id;
        i++; // doc is never undefined for query doc snapshots
        // }
      });
      const columns = [
        "Name",
        "Date",
        "Type",
        "Amount",
        "Balance",
        {
          name: "",
          options: {
            display: false
          }
        }
      ];
      const options = {
        filterType: "dropdown",
        responsive: "scroll",
        selectableRows: false
      };

      return (
        <div className="table-responsive-material">
          <ContainerHeader match={this.props.match} title={"Transactions"} />

          <MUIDataTable
            title={"Transactions"}
            data={list}
            columns={columns}
            options={options}
          />
        </div>
      );
    } else {
      return (
        <div className="loader-view">
          <CircularProgress />
        </div>
      );
    }
  }
