// export default Apply;
export default compose(
  firestoreConnect(() => [
    { collection: "transactions", orderBy: ["createdAt", "desc"] }
  ]), // or { collection: 'todos' }
  connect((state, props) => ({
    result: state.firestore.ordered.transactions
  }))
)(Apply);
