import React,{Component} from 'react'
import AppBar from "@material-ui/core/AppBar"
import ToolBar from "@material-ui/core/Toolbar"
import Grid from '@material-ui/core/Grid';
import Typography from "@material-ui/core/Typography"
import Button from '@material-ui/core/Button';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import ExpandLessIcon from "@material-ui/icons/ExpandLess";
import IconButton from '@material-ui/core/IconButton';
import Collapse from '@material-ui/core/Collapse';
const styles = theme => ({
    card: {
      maxWidth: 400,
    },
    media: {
      height: 0,
      paddingTop: '56.25%', // 16:9
    },
    actions: {
      display: 'flex',
    },
    expand: {
      transform: 'rotate(0deg)',
      transition: theme.transitions.create('transform', {
        duration: theme.transitions.duration.shortest,
      }),
      marginLeft: 'auto',
      [theme.breakpoints.up('sm')]: {
        marginRight: -8,
      },
    },
    expandOpen: {
      transform: 'rotate(180deg)',
    },
    
  });
 
export default class Cards extends Component{
    constructor(){
        super();
        this.state={
            extended:false,
        }
    }
    state = { expanded: false };
    handleExpandClick = () => {
      this.setState(state => ({ expanded: !state.expanded }));
    }

    render(){
       var ar = this.props.explanation.split('<n>'); // no separator passed to split
        return(
            <Card style={{padding:'10px',width:'350px',margin:'30px'}} >
                <CardHeader title={this.props.nameteam} subheader={this.props.category}/>
                <CardMedia style={{height:'200px'}}  image={this.props.images}  />
                  <CardActions disableActionSpacing>
                    <IconButton
                    onClick={this.handleExpandClick}
                    aria-expanded={this.state.expanded}
                    aria-label="Show more"
                    >
                    {this.state.expanded==true ?(<ExpandLessIcon/>):(<ExpandMoreIcon/>)}
       </IconButton>
        </CardActions>
        <Collapse in={this.state.expanded} timeout="auto" unmountOnExit>
          <CardContent>
          
            <Typography>
              <ul>
              {ar.map(tex=>{
               return <li><h4>{tex}</h4></li>})}
            </ul>
            </Typography>
          </CardContent>
        </Collapse>
      </Card>
        )
    }
    
}
