import { Hidden } from "@material-ui/core";
import Button from "@material-ui/core/Button";
import Grid from "@material-ui/core/Grid";
import Paper from "@material-ui/core/Paper";
import Typography from "@material-ui/core/Typography";
import { NavLink } from "react-router-dom";
import React, { Component } from "react";
import Footer from "../front/footer";
import Navbar from "../front/navbar";
import Blocks from "./carousal";

class Team extends Component {
  render() {
    return (
      <div className="NavBar">
        <React.Fragment>
          <Navbar />
          <Grid style={{ backgroundImage: "url(" + require("./b.jpg") + ")" }}>
            <Hidden mdDown>
              <Typography
                variant="h3"
                style={{
                  color: "white",
                  padding: "50px",
                  textAlign: "center",
                  fontWeight: "bold"
                }}
              >
                The Team
              </Typography>
              <Typography
                style={{
                  fontSize: "25px",
                  color: "white",
                  padding: "0 180px",
                  textAlign: "center",
                  fontWeight: "bold"
                }}
              >
                Crowd Genie: Making borrowing faster, cheaper, and simpler,
                compared to traditional financial institutions
              </Typography>

              <Typography
                style={{
                  fontSize: "25px",
                  color: "white",
                  padding: "20px 200px",
                  textAlign: "center"
                }}
              >
                Crowd Genie is a platform where we match Small and Medium size
                companies who are looking to borrow funds with investors who are
                looking for better returns than what they get from the banks.
              </Typography>
            </Hidden>

            <Hidden mdUp>
              <Typography
                variant="h3"
                style={{
                  color: "white",
                  textAlign: "center",
                  fontWeight: "bold",
                  marginBottom: "50px",
                  paddingTop: "50px"
                }}
              >
                The Team
              </Typography>
              <Typography
                style={{
                  fontSize: "25px",
                  color: "white",
                  padding: "0 ",
                  textAlign: "center",
                  fontWeight: "bold"
                }}
              >
                Crowd Genie: Making borrowing faster, cheaper, and simpler,
                compared to traditional financial institutions
              </Typography>

              <Typography
                style={{
                  fontSize: "25px",
                  color: "white",
                  textAlign: "center",
                  padding: "10px"
                }}
              >
                Crowd Genie is a platform where we match Small and Medium size
                companies who are looking to borrow funds with investors who are
                looking for better returns than what they get from the banks.
              </Typography>
            </Hidden>
            <Grid style={{ textAlign: "center", padding: "50px" }}>
              <NavLink
                to="/signup"
                style={{
                  fontSize: "1vw",
                  padding: "auto",
                  margin: "auto",
                  textDecoration: "none",
                  color: "black"
                }}
              >
                <Button
                  className="btn btn-primary jr-btn-rounded jr-btn-sm"
                  variant="contained"
                  color="primary"
                >
                  Sign Up
                </Button>
              </NavLink>
            </Grid>
          </Grid>
          <Grid>
            <Typography
              style={{
                fontSize: "30px",
                fontWeight: "bold",
                padding: "50px 0 20px 0",
                textAlign: "center"
              }}
            >
              Key Team Members
            </Typography>
            <Typography
              style={{
                fontSize: "20px",
                fontWeight: "bold",
                textAlign: "center"
              }}
            >
              Experienced entrepreneurs. Smart investors.
            </Typography>
          </Grid>
          <Blocks />

          <Paper
            style={{
              width: "100vw",
              backgroundImage: "url(" + require("../front/b.jpg") + ")",
              backgroundSize: "cover",
              textAlign: "center",
              height: "46vh",
              verticalAlign: "middle",
              marginTop: "150px",
              marginBottom: "-100px"
            }}
          >
            <div
              style={{
                height: "100%",
                verticalAlign: "middle",
                backgroundColor: "rgba(111,50,158,0.7)"
              }}
            >
              <Typography
                style={{
                  padding: "50px",
                  color: "white",
                  fontSize: "40px",
                  fontWeight: "bold"
                }}
              >
                Join our community today !
              </Typography>
              <NavLink
                to="/signup"
                style={{
                  fontSize: "1vw",
                  padding: "auto",
                  margin: "auto",
                  textDecoration: "none",
                  color: "black"
                }}
              >
                <Button
                  className="btn btn-primary jr-btn-rounded jr-btn-sm"
                  variant="contained"
                  color="primary"
                >
                  CREATE A FREE ACCOUNT
                </Button>
              </NavLink>
            </div>
          </Paper>
        </React.Fragment>
        <Footer />
      </div>
    );
  }
}

export default Team;
