import React from "react";
import { Route, Switch, withRouter } from "react-router-dom";
import { connect } from "react-redux";
import Header from "components/Header/index";
import Sidebar from "./AdminSideNav/index";
import Footer from "components/Footer";
import Dashboard from "./routes/Dashboard";
// import Widgets from "app/routes/widgets";
// import Metrics from "app/routes/metrics";
// import Components from "app/routes/components";
// import Icons from "app/routes/icons/index";
// import Form from "app/routes/form";
// import Editors from "app/routes/editors";
// import Pickers from "app/routes/pickers";
// import Extensions from "app/routes/extensions";
// import Table from "app/routes/table";
// import Chart from "app/routes/charts";
// import Map from "app/routes/map";
// import Calendar from "app/routes/calendar";
// import TimeLine from "app/routes/timeLine";
// import CustomViews from "app/routes/customViews";
// import ExtraElements from "app/routes/extraElements";
// import eCommerce from "app/routes/eCommerce";
// import AppModule from "app/routes/appModule";
// import ExtraPages from "app/routes/extraPages";
// import Tour from "../components/Tour/index";
import {
  ABOVE_THE_HEADER,
  BELOW_THE_HEADER,
  COLLAPSED_DRAWER,
  FIXED_DRAWER,
  HORIZONTAL_NAVIGATION
} from "constants/ActionTypes";
import ColorOption from "containers/Customizer/ColorOption";
import { isIOS, isMobile } from "react-device-detect";
import asyncComponent from "util/asyncComponent";
import TopNav from "components/TopNav";
import Accreditation from "./routes/Accreditation";
import CreatOfferApp from "./routes/CreateOffers";
import ListOfferApp from "./routes/OfferList";
import ReportOfferApp from "./routes/OfferReport";
import RepaymentApp from "./routes/Repayments";
import PayinApp from "./routes/Pay in";
import PayoutApp from "./routes/Pay out";
import TransactionApp from "./routes/Transactions";
import ContactApp from "./routes/contact";

import Reset from "containers/Reset";
import Verify from "containers/Verify";
class Admin extends React.Component {
  render() {
    const {
      match,
      drawerType,
      navigationStyle,
      horizontalNavPosition
    } = this.props;
    const drawerStyle = drawerType.includes(FIXED_DRAWER)
      ? "fixed-drawer"
      : drawerType.includes(COLLAPSED_DRAWER)
      ? "collapsible-drawer"
      : "mini-drawer";

    //set default height and overflow for iOS mobile Safari 10+ support.
    if (isIOS && isMobile) {
      document.body.classList.add("ios-mobile-view-height");
    } else if (document.body.classList.contains("ios-mobile-view-height")) {
      document.body.classList.remove("ios-mobile-view-height");
    }

    return (
      <div className={`app-container ${drawerStyle}`}>
        <Sidebar />
        <div className="app-main-container">
          <div
            className={`app-header ${
              navigationStyle === HORIZONTAL_NAVIGATION
                ? "app-header-horizontal"
                : ""
            }`}
          >
            {navigationStyle === HORIZONTAL_NAVIGATION &&
              horizontalNavPosition === ABOVE_THE_HEADER && (
                <TopNav styleName="app-top-header" />
              )}
            <Header />
            {navigationStyle === HORIZONTAL_NAVIGATION &&
              horizontalNavPosition === BELOW_THE_HEADER && <TopNav />}
          </div>

          <main className="app-main-content-wrapper">
            <div className="app-main-content">
              <Switch>
                <Route path={`${match.url}/verify`} component={Verify} />
                <Route path={`${match.url}/reset`} component={Reset} />

                <Route path={`${match.url}/`} component={Dashboard} exact />
                <Route
                  path={`${match.url}/Accreditation`}
                  component={Accreditation}
                />
                <Route
                  path={`${match.url}/Offer/Create`}
                  component={CreatOfferApp}
                />
                <Route
                  path={`${match.url}/Offer/Listing`}
                  component={ListOfferApp}
                />
                <Route
                  path={`${match.url}/Offer/Report`}
                  component={ReportOfferApp}
                />
                <Route
                  path={`${match.url}/Transaction/List`}
                  component={TransactionApp}
                />
                <Route
                  path={`${match.url}/Transactions/Repayment`}
                  component={RepaymentApp}
                />
                <Route
                  path={`${match.url}/Transactions/PayIn`}
                  component={PayinApp}
                />
                <Route
                  path={`${match.url}/Transactions/PayOut`}
                  component={PayoutApp}
                />
                <Route
                  path={`${match.url}/Users/List`}
                  component={ContactApp}
                />
                {/*<Route
                  path={`${match.url}/components`}
                  component={Components}
                />
                <Route path={`${match.url}/icons`} component={Icons} />
                <Route path={`${match.url}/form`} component={Form} />
                <Route path={`${match.url}/editor`} component={Editors} />
                <Route path={`${match.url}/pickers`} component={Pickers} />
                <Route
                  path={`${match.url}/extensions`}
                  component={Extensions}
                />
                <Route path={`${match.url}/table`} component={Table} />
                <Route path={`${match.url}/chart`} component={Chart} />
                <Route path={`${match.url}/map`} component={Map} />
                <Route path={`${match.url}/calendar`} component={Calendar} />
                <Route path={`${match.url}/time-line`} component={TimeLine} />
                <Route
                  path={`${match.url}/custom-views`}
                  component={CustomViews}
                />

                <Route path={`${match.url}/widgets`} component={Widgets} />
                <Route path={`${match.url}/metrics`} component={Metrics} />
                <Route
                  path={`${match.url}/extra-elements`}
                  component={ExtraElements}
                />
                <Route path={`${match.url}/ecommerce`} component={eCommerce} />
                <Route path={`${match.url}/app-module`} component={AppModule} />
                <Route
                  path={`${match.url}/to-do`}
                  component={asyncComponent(() =>
                    import("./routes/todo/basic/index")
                  )}
                />
                <Route
                  path={`${match.url}/to-do-redux`}
                  component={asyncComponent(() =>
                    import("./routes/todo/redux/index")
                  )}
                />
                <Route
                  path={`${match.url}/mail`}
                  component={asyncComponent(() =>
                    import("./routes/mail/basic/index")
                  )}
                />
                <Route
                  path={`${match.url}/mail-redux`}
                  component={asyncComponent(() =>
                    import("./routes/mail/redux/index")
                  )}
                />
                <Route
                  path={`${match.url}/chat`}
                  component={asyncComponent(() =>
                    import("./routes/chatPanel/basic/index")
                  )}
                />
                <Route
                  path={`${match.url}/chat-redux`}
                  component={asyncComponent(() =>
                    import("./routes/chatPanel/redux/index")
                  )}
                />
                <Route
                  path={`${match.url}/contact`}
                  component={asyncComponent(() =>
                    import("./routes/contact/basic/index")
                  )}
                />
                <Route
                  path={`${match.url}/contact-redux`}
                  component={asyncComponent(() =>
                    import("./routes/contact/redux/index")
                  )}
                />
                <Route
                  path={`${match.url}/extra-pages`}
                  component={ExtraPages}
                />*/}
                <Route
                  component={asyncComponent(() =>
                    import("app/routes/extraPages/routes/404")
                  )}
                />
              </Switch>
            </div>
            {/* <Footer /> */}
          </main>
        </div>
        <ColorOption />
      </div>
    );
  }
}

const mapStateToProps = ({ settings }) => {
  const { drawerType, navigationStyle, horizontalNavPosition } = settings;
  return { drawerType, navigationStyle, horizontalNavPosition };
};
export default withRouter(connect(mapStateToProps)(Admin));
