export default class Cards extends Component {
  constructor() {
    super();
    this.state = {
      extended: false
    };
  }
  state = { expanded: false };
  handleExpandClick = () => {
    this.setState(state => ({ expanded: !state.expanded }));
  };
  render() {
    return (
      <Card
        color="primary"
        raised
        style={{ padding: "10px", width: "350px", margin: "30px auto" }}
        zdepth={3}
      >
        <CardHeader title={this.props.title} />
        <CardMedia style={{ height: "200px" }} image={this.props.images} />
        <CardActions disableActionSpacing>
          <IconButton
            onClick={this.handleExpandClick}
            aria-expanded={this.state.expanded}
            aria-label="Show more"
          >
            <i className="fas fa-caret-down" />
          </IconButton>
        </CardActions>
        <Collapse in={this.state.expanded} timeout="auto" unmountOnExit>
          <CardContent>
            <Typography paragraph>Industry: {this.props.industry}</Typography>
            <Typography paragraph>
              Invested to date: {this.props.investor}
            </Typography>
            <Typography paragraph>Days left: {this.props.days}</Typography>
            <Typography paragraph>
              Investor's Effective Return: {this.props.effective}
            </Typography>
            <Typography paragraph>Loan Tenor: {this.props.tenor}</Typography>
          </CardContent>
        </Collapse>
      </Card>
    );
  }
}
