import React, { Component } from "react";
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";
import { NavLink } from "react-router-dom";
import Slider from "react-slick";

import Button from "@material-ui/core/Button";
import Hidden from "@material-ui/core/Hidden";
import "./style.css";
import firebase from "firebase";
class TextCarousal extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: [],
      mainscene: null
    };
  }
  async componentDidMount() {
    await firebase
      .database()
      .ref("/blog")
      .once("value", snapshot => {
        var a = snapshot.val();
        var main = [];

        if (a.length !== 0) {
          const keys = Object.keys(a);
          for (let i = 0; i < keys.length; i++) {
            main.push([a[keys[i]]]);
          }
          this.setState({
            data: main
          });
        }
      });
  }
  render() {
    var a = {
      dots: true,
      infinite: true,
      speed: 500,
      slidesToShow: 1,
      slidesToScroll: 1,
      autoplay: true,
      adaptiveHeight: true
    };
    const main =
      this.state.data.length !== 0 ? (
        this.state.data.map(data => {
          return (
            <div style={{ textAlign: "center" }}>
              <Typography
                variant="h3"
                style={{ color: "white", margin: "30", textAlign: "center" }}
              >
                {" "}
                {data[0].titleblog}
              </Typography>
              <Typography
                style={{ fontSize: "20px", color: "white", margin: "30px" }}
              >
                {" "}
                {data[0].detailsblog.substring(0, 300)}...
              </Typography>
              <Grid style={{ textAlign: "center" }}>
                <NavLink to={"/blog/" + data[0].titleblog}>
                  {" "}
                  <Button
                    className="btn btn-primary jr-btn-rounded jr-btn-sm"
                    style={{ textAlign: "center", margin: "auto" }}
                    variant="contained"
                    color="white"
                  >
                    Read More
                  </Button>
                </NavLink>
              </Grid>
            </div>
          );
        })
      ) : (
        <div> </div>
      );

    return (
      <Grid container md={12} xs={12} xs={12} xl={12} lg={12}>
        <Grid
          item
          md={12}
          xs={12}
          xs={12}
          xl={12}
          lg={12}
          style={{ background: "rgba(61,27,87)", padding: "100px 140px" }}
        >
          <Slider {...a}>{main}</Slider>
        </Grid>
      </Grid>
    );
  }
}

export default TextCarousal;
