class Statistics extends Component {
  constructor() {
    super();
    this.state = {
      value: 0,
      width: null
    };

    this.handleChange = this.handleChange.bind(this);
    this.onDocumentLoad = this.onDocumentLoad.bind(this);
  }
  handleChange = (event, value) => {
    this.setState({ value });
  };
  onDocumentLoad = ({ numPages }) => {
    console.log(numPages);
    this.setState({ numPages });
  };
  componentDidMount() {
    this.setDivSize();
    window.addEventListener("resize", throttle(this.setDivSize, 500));
  }

  componentWillUnmount() {
    window.removeEventListener("resize", throttle(this.setDivSize, 500));
  }

  setDivSize = () => {
    this.setState({ width: this.pdfWrapper.getBoundingClientRect().width });
  };

  render() {
    const { value } = this.state;
    return (
      <div className="NavBar">
        <div>
          <Navbar />
          <Paper
            style={{
              backgroundImage: "url(" + require("../front/b.jpg") + ")",
              backgroundSize: "cover",
              textAlign: "center",
              height: "42vh",
              verticalAlign: "middle"
            }}
          >
            <div
              style={{
                height: "100%",
                verticalAlign: "middle",
                backgroundColor: "rgba(111,50,158,0.7)"
              }}
            >
              <Typography
                style={{
                  padding: "80px",
                  color: "white",
                  fontSize: "40px",
                  fontWeight: "bold"
                }}
              >
                Statistics
              </Typography>
            </div>
          </Paper>
        </div>
        <div>
          <div
            id="row"
            style={{
              width: "100vw",
              display: "flex"
            }}
          >
            <div
              id="placeholderWrapper"
              style={{ width: "4vw", height: "100vh" }}
            />
            <div
              id="pdfWrapper"
              style={{ width: "90vw" }}
              ref={ref => (this.pdfWrapper = ref)}
            >
              <PdfComponent wrapperDivSize={this.state.width} />
            </div>
          </div>
        </div>
        <Footer />
      </div>
    );
  }
}
class PdfComponent extends Component {
  render() {
    return (
      <div>
        <Document file={Docfile}>
          <Page pageIndex={0} width={this.props.wrapperDivSize} />
        </Document>
      </div>
    );
  }
}

export default Statistics;
