import React, { Component } from "react";
import { NavLink, withRouter } from "react-router-dom";
import Button from "@material-ui/core/Button";

import IntlMessages from "util/IntlMessages";
import CustomScrollbars from "util/CustomScrollbars";

class SidenavContent extends Component {
  componentDidMount() {
    const { history } = this.props;
    const that = this;
    const pathname = `${history.location.pathname}`; // get current path

    const menuLi = document.getElementsByClassName("menu");
    for (let i = 0; i < menuLi.length; i++) {
      menuLi[i].onclick = function(event) {
        for (let j = 0; j < menuLi.length; j++) {
          const parentLi = that.closest(this, "li");
          if (
            menuLi[j] !== this &&
            (parentLi === null || !parentLi.classList.contains("open"))
          ) {
            menuLi[j].classList.remove("open");
          }
        }
        this.classList.toggle("open");
      };
    }

    const activeLi = document.querySelector('a[href="' + pathname + '"]'); // select current a element
    try {
      const activeNav = this.closest(activeLi, "ul"); // select closest ul
      if (activeNav.classList.contains("sub-menu")) {
        this.closest(activeNav, "li").classList.add("open");
      } else {
        this.closest(activeLi, "li").classList.add("open");
      }
    } catch (error) {}
  }

  componentWillReceiveProps(nextProps) {
    const { history } = nextProps;
    const pathname = `${history.location.pathname}`; // get current path

    const activeLi = document.querySelector('a[href="' + pathname + '"]'); // select current a element
    try {
      const activeNav = this.closest(activeLi, "ul"); // select closest ul
      if (activeNav.classList.contains("sub-menu")) {
        this.closest(activeNav, "li").classList.add("open");
      } else {
        this.closest(activeLi, "li").classList.add("open");
      }
    } catch (error) {}
  }

  closest(el, selector) {
    try {
      let matchesFn;
      // find vendor prefix
      [
        "matches",
        "webkitMatchesSelector",
        "mozMatchesSelector",
        "msMatchesSelector",
        "oMatchesSelector"
      ].some(function(fn) {
        if (typeof document.body[fn] == "function") {
          matchesFn = fn;
          return true;
        }
        return false;
      });

      let parent;

      // traverse parents
      while (el) {
        parent = el.parentElement;
        if (parent && parent[matchesFn](selector)) {
          return parent;
        }
        el = parent;
      }
    } catch (e) {}

    return null;
  }

  render() {
    return (
      <CustomScrollbars className=" scrollbar">
        <ul className="nav-menu">
          <li className="nav-header">{"Accreditation's"}</li>
          <li className="menu no-arrow">
            <NavLink to="/Admin/Accreditation">
              <i className="zmdi zmdi-email zmdi-hc-fw" />
              <span className="nav-text">{"List of Accreditation"}</span>
            </NavLink>
          </li>
          <li className="nav-header">{"Offers"}</li>
          <li className="menu no-arrow">
            <NavLink to="/Admin/Offer/Create">
              <i className="zmdi zmdi-email zmdi-hc-fw" />
              <span className="nav-text">{"Create Offer"}</span>
            </NavLink>
          </li>
          <li className="menu no-arrow">
            <NavLink to="/Admin/Offer/Listing">
              <i className="zmdi zmdi-email zmdi-hc-fw" />
              <span className="nav-text">{"List of Offer"}</span>
            </NavLink>
          </li>
          <li className="menu no-arrow">
            <NavLink to="/Admin/Offer/Report">
              <i className="zmdi zmdi-email zmdi-hc-fw" />
              <span className="nav-text">{"Offering Report"}</span>
            </NavLink>
          </li>

          <li className="nav-header">{"Transactions"}</li>
          <li className="menu no-arrow">
            <NavLink to="/Admin/Transaction/List">
              <i className="zmdi zmdi-check-square zmdi-hc-fw" />
              <span className="nav-text">{"All Transactions"}</span>
            </NavLink>
          </li>

          <li className="menu no-arrow">
            <NavLink to="/Admin/Transactions/Repayment">
              <i className="zmdi zmdi-email zmdi-hc-fw" />
              <span className="nav-text">{"List of Repayments"}</span>
            </NavLink>
          </li>

          <li className="menu no-arrow">
            <NavLink to="/Admin/Transactions/PayIn">
              <i className="zmdi zmdi-comment zmdi-hc-fw" />
              <span className="nav-text">{"PAY-IN"}</span>
            </NavLink>
          </li>

          <li className="menu no-arrow">
            <NavLink to="/Admin/Transactions/PayOut">
              <i className="zmdi zmdi-account-box zmdi-hc-fw" />
              <span className="nav-text">{"PAY-OUT"}</span>
            </NavLink>
          </li>

          <li className="nav-header">{"Users"}</li>
          <li className="menu no-arrow">
            <NavLink to="/Admin/Users/List">
              <i className="zmdi zmdi-account-box zmdi-hc-fw" />
              <span className="nav-text">{"List of Users"}</span>
            </NavLink>
          </li>

          <li className="nav-header">{"Others"}</li>
          <li className="menu no-arrow">
            <NavLink to="/Admin/ResetPassword">
              <i className="zmdi zmdi-account-box zmdi-hc-fw" />
              <span className="nav-text">{"Reset Password"}</span>
            </NavLink>
          </li>
          <li className="menu no-arrow">
            <NavLink to="/Admin/EmailVerify">
              <i className="zmdi zmdi-account-box zmdi-hc-fw" />
              <span className="nav-text">{"Verify Email"}</span>
            </NavLink>
          </li>
        </ul>
      </CustomScrollbars>
    );
  }
}

export default withRouter(SidenavContent);
