class EditUser extends Component {
  state = {
    open: false,
    AutoInv: false,
    Amount: 0,
    amountStatus: false
  };

  handleRequestClose = () => {
    this.setState({
      open: false
    });
    this.props.Update();
  };

  switchChange = name => (event, checked) => {
    this.setState({ [name]: checked });
    if (checked === false) {
      this.setState({ Amount: 0 });
    }
  };
  handleChange = event => {
    if (+event.target.value <= +this.props.value.wallet) {
      this.setState({ amountStatus: false });
    } else {
      this.setState({ amountStatus: true });
    }
    this.setState({ Amount: event.target.value });
  };
  UpdateUser = async () => {
    let updateData = {
      autoInvest: {
        amount: this.state.Amount,
        status: this.state.AutoInv,
        time: objToday
      }
    };
    await firebase
      .firestore()
      .collection("users")
      .doc(this.props.value.sub)
      .set(updateData, { merge: true })
      .then(this.creatAutoInvest())
      .catch(error => {
        console.log(error);
      });
  };
  creatAutoInvest = async () => {
    let newautoInv = {
      user_id: this.props.value.sub,
      amount: this.state.Amount,
      status: this.state.AutoInv
    };
    console.log(newautoInv);
    await firebase
      .firestore()
      .collection("autoInvests")
      .add(newautoInv)
      .then(this.handleRequestClose())
      .catch(error => {
        console.log(error);
      });
  };
  Submit = () => {
    if (
      this.state.AutoInv !== this.props.value.status ||
      +this.state.Amount !== +this.props.value.amount
    ) {
      if (
        (this.state.Amount > 0 || this.state.AutoInv === false) &&
        this.state.amountStatus === false
      ) {
        NotificationManager.success(
          <IntlMessages id="notification.successMessage" />,
          "Your changes has been Updated",
          2500
        );
        this.UpdateUser();
      } else {
        NotificationManager.error(
          "Enter A Valid Amount",
          <IntlMessages id="notification.errorMessage" />,
          2500
        );
      }
    } else {
      NotificationManager.info(
        "No Changes to Save",
        <IntlMessages id="notification.info" />,
        2500
      );
    }
  };
  render() {
    if (this.props.value) {
      return (
        <div>
          <Button
            variant="contained"
            className="btn btn-primary jr-btn-rounded jr-btn-sm"
            onClick={() =>
              this.setState({
                open: true,
                Amount: this.props.value.amount,
                AutoInv: this.props.value.status
              })
            }
          >
            {" "}
            Auto Invest{" "}
          </Button>{" "}
          <Dialog open={this.state.open} onClose={this.handleRequestClose}>
            <DialogTitle> Auto Invest {this.props.value.status} </DialogTitle>{" "}
            <DialogActions>
              <div className="col">
                <div className="row-lg-3 row-sm-6 row-12">
                  <Switch
                    classes={{
                      checked: "text-primary",
                      bar: "bg-primary"
                    }}
                    checked={this.state.AutoInv}
                    onChange={this.switchChange("AutoInv")}
                    aria-label="AutoInv"
                  />

                  {this.state.AutoInv === true ? "Enabled" : "Disabled"}
                </div>
                <div className="row-lg-3 row-sm-6 row-12">
                  <TextField
                    margin="dense"
                    name="Amount"
                    label="Amount($)"
                    onChange={event => {
                      this.handleChange(event);
                    }}
                    type={NumberFormat}
                    disabled={!this.state.AutoInv}
                    error={this.state.amountStatus}
                    helperText={
                      this.state.amountStatus === true
                        ? "Entered amount in greater  than amount in the wallet"
                        : ""
                    }
                    value={this.state.Amount}
                    InputProps={{
                      inputComponent: NumberFormatCustom
                    }}
                    fullWidth
                  />
                </div>
                <Button
                  onClick={this.Submit}
                  className="btn btn-primary jr-btn-rounded jr-btn-sm"
                >
                  Save
                </Button>
              </div>
            </DialogActions>{" "}
            <NotificationContainer />
          </Dialog>{" "}
        </div>
      );
    } else {
      return <div />;
    }
  }
}

class CustomToolbar extends React.Component {
  state = {
    open: false
  };

  handleClick = () => {
    console.log("clicked on icon!");
    this.setState({
      open: true
    });
  };
  handleRequestClose = () => {
    this.setState({
      open: false
    });
  };

  render() {
    const { classes } = this.props;

    return (
      <React.Fragment>
        <IconButton onClick={this.handleClick}>
          <AddIcon name="Add Users" />
        </IconButton>{" "}
        <Dialog open={this.state.open} onClose={this.handleRequestClose}>
          <DialogTitle> Add Users </DialogTitle>{" "}
          <DialogContent>
            <TextField
              autoFocus
              margin="dense"
              id="name"
              label="Email Address"
              type="email"
              fullWidth
            />
          </DialogContent>{" "}
          <DialogActions>
            <Button
              className="btn jr-btn-rounded jr-btn-sm"
              onClick={this.handleRequestClose}
              color="secondary"
            >
              Cancel{" "}
            </Button>{" "}
            <Button
              className="btn btn-primary jr-btn-rounded jr-btn-sm"
              onClick={this.handleRequestClose}
              color="primary"
            >
              Subscribe{" "}
            </Button>{" "}
          </DialogActions>{" "}
        </Dialog>{" "}
      </React.Fragment>
    );
  }
}

export default ContactList;
