import React, { Component } from "react";
import { createMuiTheme, MuiThemeProvider } from "@material-ui/core/styles";
import MomentUtils from "@date-io/moment";
import { MuiPickersUtilsProvider } from "material-ui-pickers";
import { Redirect, Route, Switch } from "react-router-dom";
import { connect } from "react-redux";
import { IntlProvider } from "react-intl";
import "assets/vendors/style";
import indigoTheme from "./themes/indigoTheme";
import cyanTheme from "./themes/cyanTheme";
import orangeTheme from "./themes/orangeTheme";
import amberTheme from "./themes/amberTheme";
import pinkTheme from "./themes/pinkTheme";
import blueTheme from "./themes/blueTheme";
import purpleTheme from "./themes/purpleTheme";
import greenTheme from "./themes/greenTheme";
import darkTheme from "./themes/darkTheme";
import AppLocale from "../lngProvider";
import {
  AMBER,
  BLUE,
  CYAN,
  DARK_AMBER,
  DARK_BLUE,
  DARK_CYAN,
  DARK_DEEP_ORANGE,
  DARK_DEEP_PURPLE,
  DARK_GREEN,
  DARK_INDIGO,
  DARK_PINK,
  DEEP_ORANGE,
  DEEP_PURPLE,
  GREEN,
  INDIGO,
  PINK
} from "constants/ThemeColors";
import AccreditedInvestorApp from "cgapp/Accredited Investor";
import AdminApp from "cgapp/Admin";
import InvestorApp from "cgapp/Investor 1";
// import MainApp from "app/index";
import SignIn from "./SignIn";
import Blogread from "./LandingPage/static/blog/blogread";
import Blog from "./LandingPage/static/blog/main.js";
import contact from "./LandingPage/static/contact/main";
import education from "./LandingPage/static/education/main";
import Feature from "./LandingPage/static/Features/main";
import Home from "./LandingPage/static/front/main.js";
import Genie from "./LandingPage/static/geniesheild/main.js";
import Team from "./LandingPage/static/team/main";
import Statistics from "./LandingPage/static/Statistics/main";
import NewsTest from "./LandingPage/static/testing/news";
import SignUp from "./SignUp";
import { setInitUrl } from "../actions/Auth";
import RTL from "util/RTL";
import asyncComponent from "util/asyncComponent";

import ForgotPassword from "./ForgotPassword";
const RestrictedRoute = ({ component: Component, authUser, ...rest }) => (
  <Route
    {...rest}
    render={props =>
      authUser.isEmpty === false ? (
        <Component {...props} />
      ) : (
        <Redirect
          to={{
            pathname: "/home",
            state: { from: props.location }
          }}
        />
      )
    }
  />
);

class App extends Component {
  //   constructor(props) {
  //   super(props);
  //   // Don't call this.setState() here!
  //   this.state = { counter: 0 };
  //   this.handleClick = this.handleClick.bind(this);
  // }
  componentWillMount() {
    window.__MUI_USE_NEXT_TYPOGRAPHY_VARIANTS__ = true;
    if (this.props.initURL === "") {
      this.props.setInitUrl(this.props.history.location.pathname);
    }
  }

  getColorTheme(themeColor, applyTheme) {
    switch (themeColor) {
      case INDIGO: {
        applyTheme = createMuiTheme(indigoTheme);
        break;
      }
      case CYAN: {
        applyTheme = createMuiTheme(cyanTheme);
        break;
      }
      case AMBER: {
        applyTheme = createMuiTheme(amberTheme);
        break;
      }
      case DEEP_ORANGE: {
        applyTheme = createMuiTheme(orangeTheme);
        break;
      }
      case PINK: {
        applyTheme = createMuiTheme(pinkTheme);
        break;
      }
      case BLUE: {
        applyTheme = createMuiTheme(blueTheme);
        break;
      }
      case DEEP_PURPLE: {
        applyTheme = createMuiTheme(purpleTheme);
        break;
      }
      case GREEN: {
        applyTheme = createMuiTheme(greenTheme);
        break;
      }
      case DARK_INDIGO: {
        applyTheme = createMuiTheme(indigoTheme);
        break;
      }
      case DARK_CYAN: {
        applyTheme = createMuiTheme(cyanTheme);
        break;
      }
      case DARK_AMBER: {
        applyTheme = createMuiTheme(amberTheme);
        break;
      }
      case DARK_DEEP_ORANGE: {
        applyTheme = createMuiTheme(orangeTheme);
        break;
      }
      case DARK_PINK: {
        applyTheme = createMuiTheme(pinkTheme);
        break;
      }
      case DARK_BLUE: {
        applyTheme = createMuiTheme(blueTheme);
        break;
      }
      case DARK_DEEP_PURPLE: {
        applyTheme = createMuiTheme(purpleTheme);
        break;
      }
      case DARK_GREEN: {
        applyTheme = createMuiTheme(greenTheme);
        break;
      }
      default:
        createMuiTheme(indigoTheme);
    }
    return applyTheme;
  }

  render() {
    const {
      match,
      location,
      themeColor,
      isDarkTheme,
      locale,
      auth,
      initURL,
      isDirectionRTL,
      profile
    } = this.props;
    let applyTheme = createMuiTheme(indigoTheme);
    if (isDarkTheme) {
      document.body.classList.add("dark-theme");
      applyTheme = createMuiTheme(darkTheme);
    } else {
      applyTheme = this.getColorTheme(themeColor, applyTheme);
    }
    if (location.pathname === "/") {
      if (auth.isEmpty === true) {
        return <Redirect to={"/home"} />;
      } else if (
        initURL === "" ||
        initURL === "/" ||
        initURL === "/signin" ||
        initURL === "/home"
      ) {
        if (profile.isLoaded === true) {
          return <Redirect to={"/" + profile.profile} />;
        }
      } else {
        return <Redirect to={initURL} />;
      }
    }
    if (isDirectionRTL) {
      applyTheme.direction = "rtl";
      document.body.classList.add("rtl");
    } else {
      document.body.classList.remove("rtl");
      applyTheme.direction = "ltr";
    }
    console.log(this.props, "i am from app");

    const currentAppLocale = AppLocale[locale.locale];
    return (
      <MuiThemeProvider theme={applyTheme}>
        <MuiPickersUtilsProvider utils={MomentUtils}>
          <IntlProvider
            locale={currentAppLocale.locale}
            messages={currentAppLocale.messages}
          >
            <RTL>
              <div className="app-main">
                <Switch>
                  {/* <Route path={`${match.url}app`} component={MainApp} /> */}
                  <RestrictedRoute
                    path={`${match.url}Admin`}
                    authUser={auth}
                    component={AdminApp}
                  />
                  <RestrictedRoute
                    path={`${match.url}Accredited Investor`}
                    authUser={auth}
                    component={AccreditedInvestorApp}
                  />
                  <RestrictedRoute
                    path={`${match.url}Investor`}
                    authUser={auth}
                    component={InvestorApp}
                  />
                  <Route
                    path={`${match.url}forgotPassword`}
                    component={ForgotPassword}
                    exact
                  />
                  <Route path="/signin" component={SignIn} />
                  <Route path="/signup" component={SignUp} />
                  <Route path={"/home"} component={Home} />
                  <Route path={"/blog"} component={Blog} />
                  <Route path={"/test"} component={NewsTest} />
                  <Route path={"/genie"} component={Genie} />
                  <Route path={"/team"} component={Team} />
                  <Route path={"/education"} component={education} />
                  <Route path={"/contact"} component={contact} />
                  <Route path={"/blog/:titleblog"} component={Blogread} />
                  <Route path={"/features"} component={Feature} />
                  <Route path={"/test"} component={NewsTest} />
                  <Route path={"/partner"} component={Home} />
                  <Route path={"/Statistics"} component={Statistics} />

                  <Route
                    component={asyncComponent(() =>
                      import("app/routes/extraPages/routes/404")
                    )}
                  />
                </Switch>
              </div>
            </RTL>
          </IntlProvider>
        </MuiPickersUtilsProvider>
      </MuiThemeProvider>
    );
  }
}

const mapStateToProps = ({ settings, url, firebase: { auth, profile } }) => {
  const {
    themeColor,
    sideNavColor,
    darkTheme,
    locale,
    isDirectionRTL
  } = settings;
  const { initURL } = url;
  return {
    themeColor,
    sideNavColor,
    isDarkTheme: darkTheme,
    locale,
    isDirectionRTL,
    auth,
    profile,
    initURL
  };
};

export default connect(
  mapStateToProps,
  { setInitUrl }
)(App);
