import { Hidden } from "@material-ui/core";
import AppBar from "@material-ui/core/AppBar";
import Button from "@material-ui/core/Button";
import Divider from "@material-ui/core/Divider";
import Drawer from "@material-ui/core/Drawer";
import Grid from "@material-ui/core/Grid";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import Typography from "@material-ui/core/Typography";
import React, { Component } from "react";
import { NavLink } from "react-router-dom";
import logo from "../../logo.svg";
import "./style.css";
// const styles = {
//   list: {
//     width: 250
//   },
//   fullList: {
//     width: "auto"
//   }
// };

export default class Navbar extends Component {
  state = {
    top: false,
    left: false,
    bottom: false,
    right: false
  };

  toggleDrawer = (side, open) => () => {
    this.setState({
      [side]: open
    });
  };

  render() {
    return (
      <React.Fragment>
        <div className="fullwidth">
          {/* <AppBar position="relative" color="white">
            <Grid container director="row" justify="flex-end">
              <i style={{ margin: "auto 0" }} className="fas fa-phone" />
              <Typography
                style={{
                  padding: "10px",
                  margin: "0 10px 0 0",
                  fontSize: "14px"
                }}
                color="inherit"
              >
                
                CALL +937593757
              </Typography>
            </Grid>
          </AppBar> */}

          <AppBar
            position="sticky"
            style={{ postion: "sticky" }}
            color="inherit"
          >
            <Grid container>
              <NavLink to="/home">
                <img
                  src={logo}
                  alt="main"
                  style={{
                    height: "70px",
                    marginLeft: "20px",
                    marginBottom: "15px",
                    marginRight: "400px",
                    marginTop: "20px",
                    float: "left"
                  }}
                />
              </NavLink>
              <Hidden mdDown>
                <a
                  href="/home#partner"
                  style={{
                    fontSize: "1vw",
                    padding: "auto",
                    margin: "auto",
                    textDecoration: "none",
                    color: "black"
                  }}
                >
                  <Typography variant="subtitle1" color="inherit">
                    Network
                  </Typography>
                </a>
                {/* <NavLink
                  to="/blog"
                  style={{
                    fontSize: "1vw",
                    padding: "auto",
                    margin: "auto",
                    textDecoration: "none",
                    color: "black"
                  }}
                >
                  
                  <Typography variant="subtitle1" color="inherit">
                    Blog
                  </Typography>
                </NavLink> */}
                <NavLink
                  to="/genie"
                  style={{
                    fontSize: "1vw",
                    padding: "auto",
                    margin: "auto",
                    textDecoration: "none",
                    color: "black"
                  }}
                >
                  <Typography variant="subtitle1" color="inherit">
                    GenieShield
                  </Typography>
                </NavLink>
                <NavLink
                  to="/team"
                  style={{
                    fontSize: "1vw",
                    padding: "auto",
                    margin: "auto",
                    textDecoration: "none",
                    color: "black"
                  }}
                >
                  <Typography variant="subtitle1" color="inherit">
                    The Team
                  </Typography>
                </NavLink>
                <NavLink
                  to="/Statistics"
                  style={{
                    fontSize: "1vw",
                    padding: "auto",
                    margin: "auto",
                    textDecoration: "none",
                    color: "black"
                  }}
                >
                  <Typography variant="subtitle1" color="inherit">
                    Statistics
                  </Typography>
                </NavLink>
                <NavLink
                  to="/education"
                  style={{
                    fontSize: "1vw",
                    padding: "auto",
                    margin: "auto",
                    textDecoration: "none",
                    color: "black"
                  }}
                >
                  <Typography variant="subtitle1" color="inherit">
                    FAQs
                  </Typography>
                </NavLink>
                <NavLink
                  to="/contact"
                  style={{
                    fontSize: "1vw",
                    padding: "auto",
                    margin: "auto",
                    textDecoration: "none",
                    color: "black"
                  }}
                >
                  <Typography variant="subtitle1" color="inherit">
                    Contact Us
                  </Typography>
                </NavLink>

                <NavLink
                  to="/signin"
                  style={{
                    fontSize: "1vw",
                    padding: "auto",
                    margin: "auto",
                    textDecoration: "none",
                    color: "black"
                  }}
                >
                  <Typography variant="subtitle1" color="inherit">
                    <Button
                      className="btn btn-primary jr-btn-rounded jr-btn-sm"
                      variant="contained"
                      color="primary"
                      style={{ fontSize: "1vw", margin: "auto" }}
                    >
                      Sign In
                    </Button>
                  </Typography>
                </NavLink>
              </Hidden>
              <Hidden lgUp>
                <Grid container director="row" justify="flex-end">
                  <Button
                    className="btn btn-primary jr-btn-rounded jr-btn-sm"
                    onClick={this.toggleDrawer("right", true)}
                    style={{ position: "relative", bottom: "50px" }}
                  >
                    <i style={{ fontSize: "25px" }} className="fas fa-bars" />
                  </Button>
                </Grid>
                <Drawer
                  anchor="right"
                  open={this.state.right}
                  onClose={this.toggleDrawer("right", false)}
                >
                  <div
                    tabIndex={0}
                    role="button"
                    onClick={this.toggleDrawer("right", false)}
                    onKeyDown={this.toggleDrawer("right", false)}
                  >
                    <div>
                      <List>
                        <ListItem button key={"Network"}>
                          <a href="#partner">
                            <ListItemText
                              style={{ padding: "0 20px" }}
                              primary={"Network"}
                            />
                          </a>
                        </ListItem>
                        <NavLink
                          to="/blog"
                          style={{
                            fontSize: "1vw",
                            margin: "auto",
                            textDecoration: "none",
                            color: "black"
                          }}
                        >
                          <ListItem
                            button
                            key={"Blog"}
                            style={{ padding: "auto 150px auto auto" }}
                          >
                            <ListItemText
                              style={{ padding: "0 20px" }}
                              primary={"Blog"}
                            />
                          </ListItem>
                        </NavLink>
                        <NavLink
                          to="/genie"
                          style={{
                            fontSize: "1vw",
                            padding: "auto",
                            margin: "auto",
                            textDecoration: "none",
                            color: "black"
                          }}
                        >
                          <ListItem button key={"GenieSheild"}>
                            <ListItemText
                              style={{ padding: "0 20px" }}
                              primary={"GenieSheild"}
                            />
                          </ListItem>
                        </NavLink>
                        <NavLink
                          to="/team"
                          style={{
                            fontSize: "1vw",
                            padding: "auto",
                            margin: "auto",
                            textDecoration: "none",
                            color: "black"
                          }}
                        >
                          <ListItem button key={"The Team"}>
                            <ListItemText
                              style={{ padding: "0 20px" }}
                              primary={"The Team"}
                            />
                          </ListItem>
                        </NavLink>
                        <NavLink
                          to="/education"
                          style={{
                            fontSize: "1vw",
                            padding: "auto",
                            margin: "auto",
                            textDecoration: "none",
                            color: "black"
                          }}
                        >
                          <ListItem button key={"Education Center"}>
                            <ListItemText
                              style={{ padding: "0 20px" }}
                              primary={"Education Center"}
                            />
                          </ListItem>
                        </NavLink>
                        <NavLink
                          to="/contact"
                          style={{
                            fontSize: "1vw",
                            padding: "auto",
                            margin: "auto",
                            textDecoration: "none",
                            color: "black"
                          }}
                        >
                          <ListItem button key={"Contact Us"}>
                            <ListItemText
                              style={{ padding: "0 20px" }}
                              primary={"Contact Us"}
                            />
                          </ListItem>
                        </NavLink>
                        <NavLink
                          to="/features"
                          style={{
                            fontSize: "1vw",
                            padding: "auto",
                            margin: "auto",
                            textDecoration: "none",
                            color: "black"
                          }}
                        >
                          <ListItem button key={"Features"}>
                            <ListItemText
                              style={{ padding: "0 20px" }}
                              primary={"Features"}
                            />
                          </ListItem>
                        </NavLink>
                      </List>
                      <Divider />
                      <List>
                        <ListItem button key={"Login"}>
                          <ListItemIcon>
                            <i className="fas fa-sign-in-alt" />
                          </ListItemIcon>
                          <ListItemText primary={"Login"} />
                        </ListItem>
                        <ListItem button key={"Sign Up"}>
                          <ListItemIcon>
                            <i className="fas fa-user-plus" />
                          </ListItemIcon>
                          <ListItemText primary={"Sign Up"} />
                        </ListItem>
                      </List>
                    </div>
                  </div>
                </Drawer>
              </Hidden>
            </Grid>
          </AppBar>
        </div>
      </React.Fragment>
    );
  }
}
