import Button from "@material-ui/core/Button";
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";
import React, { Component } from "react";
import { NavLink } from "react-router-dom";

class FrontImage extends Component {
  render() {
    return (
      <Grid container lg={12} style={background} justify="center">
        <Grid
          item
          lg={12}
          sm={12}
          md={12}
          xs={12}
          style={{
            height: "500px",
            display: "block",
            background: "rgba(100,100,100,0.5)"
          }}
        >
          <Typography
            style={{
              textAlign: "center",
              fontSize: "50px",
              color: "white",
              fontWeight: "bold",
              margin: "100px auto -10px auto"
            }}
          >
            Small Business
          </Typography>
          <Typography
            style={{
              textAlign: "center",
              fontSize: "50px",
              color: "white",
              fontWeight: "bold"
            }}
          >
            lending platform
          </Typography>
          <Typography
            style={{
              textAlign: "center",
              fontSize: "20px",
              color: "white",
              margin: "20px"
            }}
          >
            License holder from Monetary Authority of Singapore
          </Typography>
          <Typography
            style={{
              textAlign: "center",
              fontSize: "50px",
              color: "white",
              fontWeight: "bold"
            }}
          >
            <NavLink
              to="/signup"
              style={{
                fontSize: "1vw",
                textDecoration: "none",
                color: "black"
              }}
            >
              <Button
                className="btn btn-primary jr-btn-rounded jr-btn-sm"
                color="primary"
                variant="contained"
              >
                JOIN NOW
              </Button>
            </NavLink>
          </Typography>
        </Grid>
      </Grid>
    );
  }
}
const background = {
  backgroundImage: "url(" + require("./b.jpg") + ")",
  height: "auto",
  width: "100%",
  backgroundAttachment: "fixed",
  backgroundPosition: "center",
  backgroundRepeat: "no-repeat",
  backgroundSize: "cover",
  marginBottom: "100px"
};
export default FrontImage;
