import React, { Component } from "react";
import { Link } from "react-router-dom";
import IntlMessages from "util/IntlMessages";
import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";
import firebase from "firebase";
import {
  NotificationContainer,
  NotificationManager
} from "react-notifications";

class Reset extends Component {
  constructor() {
    super();
    this.state = {
      password1: "",
      password2: ""
    };
  }
  createNotification = type => {
    return () => {
      switch (type) {
        case "info":
          NotificationManager.info(<IntlMessages id="notification.infoMsg" />);
          break;
        case "success":
          NotificationManager.success(
            <IntlMessages id="notification.successMessage" />,
            <IntlMessages id="notification.titleHere" />
          );
          break;
        case "warning":
          NotificationManager.warning(
            <IntlMessages id="notification.warningMessage" />,
            <IntlMessages id="notification.closeAfter3000ms" />,
            3000
          );
          break;
        case "error":
          NotificationManager.error(
            <IntlMessages id="notification.errorMessage" />,
            <IntlMessages id="notification.clickMe" />,
            5000,
            () => {
              alert("callback");
            }
          );
          break;
      }
    };
  };
  ChangePass = () => {
    var user = firebase.auth().currentUser;
    user
      .updatePassword(this.state.password1)
      .then(function() {
        NotificationManager.success(
          "Password has been Saved",
          "Reset Password",
          5000
        );
      })
      .catch(function(error) {
        NotificationManager.error(error.message, "Error", 5000);
      });
    console.log(this.state.password1, user);
  };
  Submit = () => {
    if (this.state.password1 !== "" && this.state.password2 !== "") {
      if (this.state.password1 === this.state.password2) {
        this.ChangePass();
      } else {
        NotificationManager.warning(
          "Passwords Dont Match",
          "Reset Passsword",
          3000
        );
      }
    } else {
      NotificationManager.error("Please Enter All The Fields", "Error", 5000);
    }
  };
  handleChange = event => {
    var key = event.target.name;
    this.setState({ [key]: event.target.value });
  };
  render() {
    return (
      <div className="login-container d-flex justify-content-center align-items-center animated slideInUpTiny animation-duration-3">
        <div className="login-content text-center">
          <div className="mb-4">
            <h2>
              <IntlMessages id="appModule.resetPassword" />
            </h2>
          </div>

          <div className="login-form">
            <div className="form-group">
              <input
                type="password"
                placeholder="New Password"
                value={this.state.password1}
                name="password1"
                onChange={this.handleChange}
                className="form-control form-control-lg"
              />
            </div>
            <div className="form-group">
              <input
                type="password"
                placeholder="Confirm Password"
                value={this.state.password2}
                onChange={this.handleChange}
                name="password2"
                className="form-control form-control-lg"
              />
            </div>
            <div className="mt-4 mb-2">
              <Button
                onClick={this.Submit}
                className="btn btn-primary jr-btn-rounded"
              >
                <IntlMessages id="appModule.reset" />
              </Button>
            </div>
          </div>
        </div>

        <NotificationContainer />
      </div>
    );
  }
}

export default Reset;
