import { Hidden } from "@material-ui/core";
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";
import React, { Component } from "react";
import Footer from "../front/footer";
import Navbar from "../front/navbar";
import firebase from "firebase";
class Blogread extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: [],
      mainscene: null,
      urlsmain: ""
    };
  }
  async componentDidMount() {
    await firebase
      .database()
      .ref("/blog")
      .once("value", snapshot => {
        var a = snapshot.val();
        var main = [];
        if (a.length !== 0) {
          const keys = Object.keys(a);

          for (let i = 0; i < keys.length; i++) {
            console.log(a[keys[i]]);
            if (a[keys[i]].titleblog == this.props.match.params.titleblog)
              main.push([a[keys[i]]]);
          }
          this.setState({
            data: main
          });
        }
      });

    var storageref = firebase.storage().ref("/blog");

    await this.state.data.map(data => {
      storageref
        .child(data[0].titleblog)
        .child(data[0].titleblog)
        .getDownloadURL()
        .then(url => {
          this.setState({ urlsmain: this.state.urlsmain.concat(url) });
        });
    });
  }

  render() {
    const main =
      this.state.data.length !== 0 ? (
        this.state.data.map(data => {
          return (
            <Grid>
              <Typography
                variant="h3"
                style={{ color: "white", margin: "30px", textAlign: "center" }}
              >
                {" "}
                {data[0].titleblog}
              </Typography>
              <img style={{ height: "200px" }} src={this.state.urlsmain} />
              <Typography
                style={{
                  fontSize: "20px",
                  color: "white",
                  margin: "30px",
                  textAlign: "left"
                }}
              >
                {" "}
                {data[0].detailsblog}
              </Typography>
            </Grid>
          );
        })
      ) : (
        <Grid />
      );

    const mainmobile =
      this.state.data.length !== 0 ? (
        this.state.data.map(data => {
          return (
            <Grid>
              <Typography
                variant="h3"
                style={{
                  color: "black",
                  margin: "100px 230px",
                  textAlign: "center"
                }}
              >
                {" "}
                {data[0].titleblog}
              </Typography>
              <img style={{ height: "150px" }} src={this.state.urlsmain} />
              <Typography
                style={{
                  fontSize: "19px",
                  color: "black",
                  margin: "100px 250px 100px 220px",
                  textAlign: "left"
                }}
              >
                {" "}
                {data[0].detailsblog}
              </Typography>
            </Grid>
          );
        })
      ) : (
        <Grid />
      );

    return (
      <div className="NavBar">
        <Navbar />
        <Grid container lg={12}>
          <Hidden mdDown>
            <Grid
              item
              lg={12}
              style={{
                background: "rgba(61,27,87)",
                padding: "100px",
                textAlign: "center"
              }}
            >
              <Grid style={{ textAlign: "center" }}>{main}</Grid>
            </Grid>
          </Hidden>
          <Hidden mdUp>
            <Grid item md={12} xs={12} sm={12} style={{ textAlign: "center" }}>
              <Grid style={{ textAlign: "center", margin: "auto -200px" }}>
                {mainmobile}
              </Grid>
            </Grid>
          </Hidden>
        </Grid>

        <Footer />
      </div>
    );
  }
}

export default Blogread;
