import { Grid } from "@material-ui/core";
import React, { Component } from "react";
import partner1 from "./partner1.png";
import partner2 from "./partner2.png";
import partner3 from "./partner3.jpeg";
import partner4 from "./partner4.jpeg";
export default class OurNetWork extends Component {
  render() {
    return (
      <Grid container style={{ marginTop: "50px", alignItems: "center" }}>
        <Grid
          style={{ textAlign: "center", margin: "20px 0" }}
          item
          lg={3}
          md={3}
          sm={12}
          xs={12}
        >
          <img alt="partner1" height="88px" src={partner1} />
        </Grid>
        <Grid
          style={{ textAlign: "center", margin: "20px 0" }}
          item
          lg={3}
          md={3}
          sm={12}
          xs={12}
        >
          <img height="88px" alt="partner2" src={partner2} />
        </Grid>
        <Grid
          style={{ textAlign: "center", margin: "20px 0" }}
          item
          lg={3}
          md={3}
          sm={12}
          xs={12}
        >
          <img height="88px" alt="partner3" src={partner3} />
        </Grid>
        <Grid
          style={{ textAlign: "center", margin: "20px 0" }}
          item
          lg={3}
          md={3}
          sm={12}
          xs={12}
        >
          <img height="44px" alt="partner4" src={partner4} />
        </Grid>
      </Grid>
    );
  }
}
