import React,{Component} from 'react'
import {NavLink} from 'react-router-dom'
import Grid from '@material-ui/core/Grid';
import Typography from "@material-ui/core/Typography"
import Button from '@material-ui/core/Button';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import IconButton from '@material-ui/core/IconButton'
import Collapse from '@material-ui/core/Collapse'
const styles = theme => ({
    card: {
      maxWidth: 400,
    },
    media: {
      height: 0,
      paddingTop: '56.25%', // 16:9
    },
    actions: {
      display: 'flex',
    },
    expand: {
      transform: 'rotate(0deg)',
      transition: theme.transitions.create('transform', {
        duration: theme.transitions.duration.shortest,
      }),
      marginLeft: 'auto',
      [theme.breakpoints.up('sm')]: {
        marginRight: -8,
      },
    },
    expandOpen: {
      transform: 'rotate(180deg)',
    },
    
  });
 
export default class Cards extends Component{
    constructor(){
        super();
        this.state={
            extended:false,
        }
    }
    state = { expanded: false };
    handleExpandClick = () => {
      this.setState(state => ({ expanded: !state.expanded }));
    }
    render(){
       
        return(
            <Card style={{padding:'10px',width:'350px',margin:'30px'}} >
                <CardHeader title={this.props.title} subheader={this.props.dateblog}/>
                <CardMedia style={{height:'200px'}}  image={this.props.images}  />
                  <CardActions disableActionSpacing>
                  
                  <NavLink to={"/blog/"+this.props.title}>
                  <IconButton 
                    color='primary'
                    aria-label="Read More">
                    <i className="fab fa-readme"></i></IconButton>
                  </NavLink>
                    <IconButton
                    color='primary'
                    style={{float:'right'}}
                    onClick={this.handleExpandClick}
                    aria-expanded={this.state.expanded}
                    aria-label="Show more"
                    >
                      <i className="fas fa-caret-down"></i>        </IconButton>

        </CardActions>
        <Collapse in={this.state.expanded} timeout="auto" unmountOnExit>
          <CardContent >
          
            <Typography paragraph>
              {this.props.detailsblog}...
            </Typography>
          </CardContent>
 
    
        </Collapse>
      </Card>
        )
    }
    
}
