import React, { Component } from "react";
import Grid from "@material-ui/core/Grid";
import Cards from "./cards.js";
import firebase from "firebase";

class Blocks extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: [],
      urlsmain: [],
      mainscene: null
    };
    this.fetchingfirebase = this.fetchingfirebase.bind(this);
  }

  async componentDidMount() {
    await firebase
      .database()
      .ref("/team")
      .once("value", snapshot => {
        var a = snapshot.val();
        var main = [];
        if (a.length !== 0) {
          const keys = Object.keys(a);
          for (let i = 0; i < keys.length; i++) {
            main.push([a[keys[i]]]);
          }
          this.setState({
            data: main,
            urlsmain: []
          });
        }
      });

    this.fetchingfirebase(0);
  }
  async fetchingfirebase(i) {
    if (i < this.state.data.length) {
      var storageref = firebase.storage().ref("/team");

      await storageref
        .child(this.state.data[i][0].nameteam)
        .child(this.state.data[i][0].nameteam)
        .getDownloadURL()
        .then(url => {
          this.setState(prevState => ({
            urlsmain: [...prevState.urlsmain, url]
          }));
          this.fetchingfirebase(i + 1);
        });
    } else {
      return;
    }
  }

  render() {
    var i = -1;
    console.log(this.state);
    const main =
      this.state.urlsmain.length == this.state.data.length ? (
        this.state.data.map(data => {
          i = i + 1;
          return (
            <Cards
              images={this.state.urlsmain[i]}
              explanation={data[0].explanation}
              category={data[0].category}
              nameteam={data[0].nameteam}
            />
          );
        })
      ) : (
        <div> </div>
      );

    return (
      <div>
        <Grid container>{main}</Grid>
      </div>
    );
  }
}

export default Blocks;
