import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import TextField from "@material-ui/core/TextField";
import ContainerHeader from "components/ContainerHeader/index";
import { firefunction } from "firebase";
import moment from "moment";
import MUIDataTable from "mui-datatables";
import React, { Component } from "react";
import IntlMessages from "util/IntlMessages";
import FullScreen from "./FullScreen";
import { connect } from "react-redux";
import { firestoreConnect } from "react-redux-firebase";
import firebase from "firebase";
import { compose } from "redux";
import CircularProgress from "@material-ui/core/CircularProgress";

class Generate extends React.Component {
  state = {
    open: false,
    term: null,
    Fee: null,
    interest: null
  };

  handleClickOpen = () => {
    this.setState({ open: true });
  };

  handleRequestClose = () => {
    //this.props.Change();
    this.setState({ open: false });
  };
  handleChange = event => {
    let key = event.target.name;
    this.setState({ [key]: event.target.value });
  };

  Submit() {
    if (this.state.term && this.state.Fee) {
      const data = this.props.data;
      console.log("in getting called");
      let invest = [];

      for (let i = 0; i < data.investors.length; i++) {
        invest.push({
          id: data.investors[i].user_sub,
          Amount: +data.investors[i].Amount
        });
      }
      console.log(
        "try0000",
        invest,
        data.offerId,
        +data.Goal,
        +this.state.term,
        +this.state.interest,
        +this.state.Fee
      );
      let scheduleGen = firefunction.httpsCallable("startScheduleGen");
      scheduleGen({
        offering: {
          id: data.offerId,
          goal: +data.Goal,
          term: +this.state.term,
          rate: +this.state.interest,
          fee: +this.state.Fee,
          transactions: invest,
          payoutstart: Date.now(),
          note: "done"
        }
      })
        .then(data => {
          console.log(data);
          this.handleRequestClose();
        })
        .catch(err => {
          console.log(err);
        });
    }
  }

  render() {
    return (
      <div>
        <Button
          variant="contained"
          className="btn btn-primary jr-btn-rounded jr-btn-sm"
          onClick={this.handleClickOpen}
        >
          Generate Repayment
        </Button>
        <Dialog open={this.state.open} onClose={this.handleRequestClose}>
          <DialogTitle>Repayment Generation</DialogTitle>
          <DialogContent>
            <TextField
              margin="dense"
              onChange={event => {
                this.handleChange(event);
              }}
              name="Fee"
              label="Platform Fee"
              type="number"
              fullWidth
            />
            <TextField
              margin="dense"
              onChange={event => {
                this.handleChange(event);
              }}
              name="term"
              label="tenure"
              type="number"
              fullWidth
            />
            <TextField
              margin="dense"
              onChange={event => {
                this.handleChange(event);
              }}
              name="interest"
              label="Interest rate"
              type="number"
              fullWidth
            />
          </DialogContent>
          <DialogActions>
            <Button
              onClick={event => {
                this.Submit();
              }}
              className="btn btn-primary jr-btn-rounded jr-btn-sm"
            >
              Generate
            </Button>
          </DialogActions>
        </Dialog>
      </div>
    );
  }
}

