import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";
import firebase from "firebase";
import React from "react";
import {
  NotificationContainer,
  NotificationManager
} from "react-notifications";
import { Link, Redirect } from "react-router-dom";
import IntlMessages from "util/IntlMessages";

class ForgotPassword extends React.Component {
  constructor() {
    super();
    this.state = {
      email: "",
      showMessage: false,
      alertMessage: "",
      redirect: false
    };
  }
  componentDidUpdate() {
    if (this.state.showMessage) {
      setTimeout(() => {
        this.setState({ showMessage: false, alertMessage: "" });
      }, 3000);
    }
  }
  onClick = () => {
    firebase
      .auth()
      .sendPasswordResetEmail(this.state.email)
      .then(data => {
        this.setState({ redirect: true });
      })
      .catch(error => {
        this.setState({ showMessage: true, alertMessage: error.message });
      });
  };
  render() {
    if (this.state.redirect) {
      return <Redirect to={"/"} />;
    } else {
      return (
        <div className="NavBar login-container d-flex justify-content-center align-items-center animated slideInUpTiny animation-duration-3">
          <div className="login-content">
            <div className="login-header">
              <Link className="app-logo" to="/" title="Crowd Genie">
                <img src="logo.jpg" alt="Crowd Genie" title="Crowd Genie" />
              </Link>
            </div>

            <div className="mb-2">
              <h2>
                <IntlMessages id="appModule.forgotPassword" />
              </h2>
            </div>

            <div className="login-form">
              <form method="post" action="/">
                <TextField
                  type="email"
                  id="required"
                  label={<IntlMessages id="appModule.email" />}
                  fullWidth
                  defaultValue=""
                  onChange={event => {
                    this.setState({ email: event.target.value });
                  }}
                  margin="normal"
                  className="mt-0 mb-4"
                />

                <Button
                  variant="contained"
                  className="btn btn-primary jr-btn-rounded jr-btn-sm"
                  onClick={this.onClick}
                  className="text-white"
                >
                  <IntlMessages id="appModule.resetPassword" />
                </Button>
              </form>
              {this.state.showMessage &&
                NotificationManager.error(this.state.alertMessage)}
              <NotificationContainer />
            </div>
          </div>
        </div>
      );
    }
  }
}

export default ForgotPassword;
