import AppBar from "@material-ui/core/AppBar";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import IconButton from "@material-ui/core/IconButton";
import Slide from "@material-ui/core/Slide";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import CloseIcon from "@material-ui/icons/Close";
import CardBox from "components/CardBox";
import React from "react";
import moment from "moment";

import MUIDataTable from "mui-datatables";
let invArray = [];
function Transition(props) {
  return <Slide direction="up" {...props} />;
}

class FullScreenDialog extends React.Component {
  state = {
    open: false
  };

  handleClickOpen = () => {
    this.setState({ open: true });
  };

  handleRequestClose = () => {
    this.props.update();
    this.setState({ open: false });
  };

  render() {
    const columns = ["Name", "Amount", "Date"];
    const { data } = this.props;
    let i = 0;
    data.investors.map(DataA => {
      console.log(DataA.investor_Name);
      invArray[i] = [
        DataA === undefined ? "" : DataA.investor_Name,
        DataA.Amount === undefined ? "" : DataA.Amount,
        DataA.time === undefined
          ? ""
          : moment(DataA.time.toDate())
              .format("YYYY/MM/DD")
              .toString()
      ];
      i++;
    });

    const options = {
      filter: "dropdown",
      responsive: "scroll",
      selectableRows: false
    };
    return (
      <div>
        <Button
          variant="contained"
          className="btn btn-primary jr-btn-rounded jr-btn-sm"
          onClick={this.handleClickOpen}
        >
          See Investors Details
        </Button>
        <Dialog
          fullScreen
          open={this.state.open}
          onClose={this.handleRequestClose}
          TransitionComponent={Transition}
        >
          <AppBar className="position-relative bg-primary">
            <Toolbar>
              <IconButton onClick={this.handleRequestClose} aria-label="Close">
                <CloseIcon />
              </IconButton>
              <Typography
                type="title"
                color="inherit"
                style={{
                  flex: 1
                }}
              >
                List of Investors
              </Typography>
            </Toolbar>
          </AppBar>
          <CardBox
            styleName="col-lg-12"
            childrenStyle="d-flex justify-content"
            headerOutside
          >
            <div className="w-100">
              <MUIDataTable
                title={"Investors List"}
                data={invArray}
                columns={columns}
                options={options}
              />
            </div>
          </CardBox>
        </Dialog>
      </div>
    );
  }
}

export default FullScreenDialog;
