import React, { Component } from "react";
import { connect } from "react-redux";
import TextField from "@material-ui/core/TextField";
import Button from "@material-ui/core/Button";
import {
  NotificationContainer,
  NotificationManager
} from "react-notifications";
import CircularProgress from "@material-ui/core/CircularProgress";
import { Link } from "react-router-dom";
import IntlMessages from "util/IntlMessages";
// import {
//   hideMessage,
//   showAuthLoader,
//   userFacebookSignIn,
//   userGithubSignIn,
//   userGoogleSignIn,
//   userSignUp,
//   userTwitterSignIn
// } from 'actions/Auth';
import firebase from "firebase";

class SignUp extends Component {
  constructor() {
    super();
    this.state = {
      name: "",
      email: "",
      password: "",
      loader: false,
      showMessage: false,
      alertMessage: ""
    };
  }

  componentDidUpdate() {
    if (this.state.showMessage) {
      setTimeout(() => {
        this.setState({ showMessage: false, alertMessage: "" });
      }, 3000);
    }
    // if (
    //   this.props.auth.isEmpty === false &&
    //   this.props.profile.isEmpty === false
    // ) {
    //   console.log(this.props.profile);
    //   this.props.history.push("/" + this.props.profile.profile);
    // }
  }

  createNewUser = ({ email, password, username, profile }) => {
    this.setState({ loader: true });
    firebase
      .createUser({ email, password, username }, { username, email, profile })
      .then(data => {
        this.setState({ loader: false });
      })
      .catch(err => {
        this.setState({
          loader: false,
          showMessage: true,
          alertMessage: err.message
        });
      });
    // console.log(email, password, username);
  };
  render() {
    const {
      name,
      email,
      password,
      showMessage,
      loader,
      alertMessage
    } = this.state;
    if (
      this.props.auth.isEmpty === false &&
      this.props.profile.isEmpty === false
    ) {
      console.log(this.props.profile);
      this.props.history.push("/" + this.props.profile.profile);
    }
    return (
      <div className="app-login-container d-flex justify-content-center align-items-center animated slideInUpTiny animation-duration-3">
        <div className="app-login-main-content">
          <div
            className="app-logo-content d-flex align-items-center justify-content-center"
            style={{
              background: "white",
              "border-left-width": "thick",
              "border-left-color": "#662d91",
              "border-left-style": "solid"
            }}
          >
            <Link className="logo-lg" to="/" title="Jambo">
              <img
                src={require("assets/images/logo.jpg")}
                alt="Crowd Genie"
                height="150"
                width="203"
                title="Crowd Genie"
              />
            </Link>
          </div>

          <div className="app-login-content">
            <div className="app-login-header">
              <h1>Sign Up</h1>
            </div>

            <div className="mb-4">
              <h2>
                <IntlMessages id="appModule.createAccount" />
              </h2>
            </div>

            <div className="app-login-form">
              <form method="post" action="/">
                <TextField
                  type="text"
                  label="Name"
                  onChange={event =>
                    this.setState({ name: event.target.value })
                  }
                  fullWidth
                  defaultValue={name}
                  margin="normal"
                  className="mt-0 mb-2"
                />

                <TextField
                  type="email"
                  onChange={event =>
                    this.setState({ email: event.target.value })
                  }
                  label={<IntlMessages id="appModule.email" />}
                  fullWidth
                  defaultValue={email}
                  margin="normal"
                  className="mt-0 mb-2"
                />

                <TextField
                  type="password"
                  onChange={event =>
                    this.setState({ password: event.target.value })
                  }
                  label={<IntlMessages id="appModule.password" />}
                  fullWidth
                  defaultValue={password}
                  margin="normal"
                  className="mt-0 mb-4"
                />

                <div className="mb-3 d-flex align-items-center justify-content-between">
                  <Button
                    variant="contained"
                    onClick={event => {
                      // this.props.showAuthLoader();
                      // this.props.userSignUp({email, password});
                      this.createNewUser({
                        email: email,
                        password: password,
                        username: name,
                        profile: "Admin"
                      });
                    }}
                    className="btn btn-primary jr-btn-rounded jr-btn-sm"
                    style={{ "background-color": "#662d91", color: "white" }}
                  >
                    <IntlMessages id="appModule.regsiter" />
                  </Button>
                  <Link to="/signin">
                    <IntlMessages id="signUp.alreadyMember" />
                  </Link>
                </div>
              </form>
            </div>
          </div>
        </div>

        {loader && (
          <div className="loader-view">
            <CircularProgress />
          </div>
        )}
        {showMessage && NotificationManager.error(alertMessage)}
        <NotificationContainer />
      </div>
    );
  }
}

export default connect(
  // Map state to props
  ({ firebase: { auth, profile } }) => ({
    auth,
    profile
  })
)(SignUp);
